<template>
    <div class="nurse_form">
      <!--  -->
      <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" style="width:500px" class="demo-ruleForm">
      <el-form-item label="" prop="name">
        <el-input v-model="ruleForm.name"></el-input>
      </el-form-item>
      <el-form-item label="" prop="address">
        <el-input v-model="ruleForm.address"></el-input>
      </el-form-item>
      <el-form-item label="" prop="age">
        <el-input v-model="ruleForm.age"></el-input>
      </el-form-item>
      <el-form-item label="" prop="position">
        <el-input v-model="ruleForm.position"></el-input>
      </el-form-item>
      <el-form-item label="" prop="profession">
        <el-input v-model="ruleForm.profession"></el-input>
      </el-form-item>
      <el-form-item label="" prop="department">
        <el-select v-model="ruleForm.department" placeholder="">
            <el-option label="" value="women"></el-option>
            <el-option label="" value="child"></el-option>
            <el-option label="" value="birth"></el-option>
            <el-option label="" value="emergency"></el-option>
            <el-option label="" value="spirit"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="" prop="education">
        <el-select v-model="ruleForm.education" placeholder="">
            <el-option label="" value="technical"></el-option>
            <el-option label="" value="junior"></el-option>
            <el-option label="" value="undergraduate"></el-option>
            <el-option label="" value="master"></el-option>
            <el-option label="" value="doctor"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="" required>
        <el-col :span="11">
          <el-form-item prop="date">
            <el-date-picker type="date" placeholder="" v-model="ruleForm.date">
            </el-date-picker>
          </el-form-item>
        </el-col>
      </el-form-item>
      <el-form-item label="" prop="sex">
        <el-radio-group v-model="ruleForm.sex">
          <el-radio label=""></el-radio>
          <el-radio label=""></el-radio>
        </el-radio-group>
      </el-form-item>     
      <el-form-item>
        <el-button type="primary" @click="submitForm('ruleForm')"></el-button>
        <el-button @click="resetForm('ruleForm')"></el-button>
      </el-form-item>
      </el-form>
    </div>
</template>

<script type="text/ecmascript-6">
export default {
  data () {
    return {
      ruleForm: {
        name: '',
        address: '',
        age: '',
        position: '',
        profession: '',
        department: '',
        education: '',
        sex: '',
        date: ''
      },
      rules: {
        name: [
          {required: true, message: '', trigger: 'blur'},
          {min: 2, max: 5, message: '35', trigger: 'blur'}
        ],
        address: [
          {required: true, message: '', trigger: 'blur'}
        ],
        age: [
          {required: true, message: '', trigger: 'blur'}
          // {type: 'number', message: ''}
        ],
        position: [
          {required: true, message: '', trigger: 'blur'}
        ],
        profession: [
          {required: true, message: '', trigger: 'blur'}
        ],
        department: [
          {required: true, message: '', trigger: 'change'}
        ],
        education: [
          {required: true, message: '', trigger: 'change'}
        ],
        sex: [
          {required: true, message: '', trigger: 'change'}
        ],
        date: [
          {type: 'date', required: true, message: '', trigger: 'change'}
        ]
      }
    };
  },
  methods: {
    // 
    submitForm (formName) {
      this.timeFormat();
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.$message({
            message: '',
            type: 'success'
          });
          console.log(this.ruleForm);
        } else {
          this.$message.error('');
          return false;
        }
      });
    },
    // 
    resetForm (formName) {
      this.$refs[formName].resetFields();
    },
    timeFormat () {
      let oldDate = this.ruleForm.date;
      let year = new Date(oldDate).getFullYear();
      let month = new Date(oldDate).getMonth() + 1;
      let day = new Date(oldDate).getDate();
      let dateFormat = year + '-' + month + '-' + day;
      console.log(dateFormat);
    }
  }
};
</script>

<style lang="stylus-loader" rel="stylesheet/stylus">
</style>



