<template>
  <div class="staffIntroduction">
    <el-tabs v-model="activeName2" type="card" @tab-click="handleClick">
      <el-tab-pane label="" name="first">
        <span class="title"><em></em></span>
        <div class="input">
          <el-input size="mini" type="text" placeholder="" v-model="input" style="width:150px"></el-input>
          <el-input size="mini" type="text" placeholder="" v-model="input" style="width:150px"></el-input>
          <el-input size="mini" type="text" placeholder="" v-model="input" style="width:150px"></el-input>
          <el-button size="mini" type="primary" @click="Onsubmit"></el-button>
        </div>
        <div class="table">
           <el-table ref="multipleTable" :data="table" border tooltip-effect="dark"  style="width: 100%">
              <el-table-column type="expand">
                <template scope="props">
                  <el-form label-position="left" inline class="demo-table-expand">
                    <el-form-item label=":">
                      <span>{{ props.row.message }}</span>
                    </el-form-item>
                  </el-form>
                </template>
              </el-table-column>
              <el-table-column label="" width="50" >
                <template scope="scope">{{ scope.$index }}</template>
              </el-table-column>
              <el-table-column prop="name" label=""  width="120" ></el-table-column>
              <el-table-column prop="sex" label="" show-overflow-tooltip width="70"></el-table-column>
              <el-table-column prop="age" label="" sortable show-overflow-tooltip width="100"></el-table-column>
              <el-table-column prop="position" label="" show-overflow-tooltip width="150"></el-table-column>
              <el-table-column prop="department" label="" show-overflow-tooltip></el-table-column>
               <el-table-column prop="area" label="" show-overflow-tooltip></el-table-column>
              <el-table-column prop="special" label="" show-overflow-tooltip></el-table-column>
              <el-table-column align="center" label="">
                <template scope="scope">
                  <img src="../logo/phone.icon.jpg" width="20" height="20">
                  <el-button size="small" @click="handleOrder(scope.$index)"></el-button>
                </template>
              </el-table-column>
           </el-table>
        </div>       
      </el-tab-pane>
      <el-tab-pane label="" name="second">
        <span class="title"><em></em></span>
        <div class="input">
          <el-input size="mini" type="text" placeholder="" v-model="input" style="width:150px"></el-input>
          <el-input size="mini" type="text" placeholder="" v-model="input" style="width:150px"></el-input>
          <el-input size="mini" type="text" placeholder="" v-model="input" style="width:150px"></el-input>
          <el-button size="mini" type="primary" @click="Onsubmit"></el-button>
        </div> 
         <div class="table2">
           <el-table ref="multipleTable" :data="table2" border tooltip-effect="dark"  style="width: 100%">
              <el-table-column type="expand">
                <template scope="props">
                  <el-form label-position="left" inline class="demo-table-expand">
                    <el-form-item label=":">
                      <span>{{ props.row.message }}</span>
                    </el-form-item>
                  </el-form>
                </template>
              </el-table-column>
              <el-table-column label="" width="50" >
                <template scope="scope">{{ scope.$index }}</template>
              </el-table-column>
              <el-table-column prop="name" label=""  width="120" ></el-table-column>
              <el-table-column prop="sex" label="" show-overflow-tooltip width="70"></el-table-column>
              <el-table-column prop="age" label="" sortable show-overflow-tooltip width="100"></el-table-column>
              <el-table-column prop="position" label="" show-overflow-tooltip width="150"></el-table-column>
              <el-table-column prop="department" label="" show-overflow-tooltip></el-table-column>
               <el-table-column prop="area" label="" show-overflow-tooltip></el-table-column>
              <el-table-column prop="special" label="" show-overflow-tooltip></el-table-column>
              <el-table-column align="center" label="">
                <template scope="scope">
                  <img src="../logo/phone.icon.jpg" width="20" height="20">
                  <el-button size="small" @click="handleOrder(scope.$index)"></el-button>
                </template>
              </el-table-column>
           </el-table>
        </div>       
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script type="text/ecmascript-6">
  import {api} from '../../../global/api.js';
  export default {
    data () {
      return {
        activeName2: 'first',
        input: '',
        table: [],
        table2: []
      };
    },
    methods: {
      handleClick (tab, event) {
        console.log(tab, event);
      },
      Onsubmit () {
        this.$message({
          message: '...',
          type: 'success'
        });
      },
      handleOrder () {
        this.$message({
          message: '...',
          type: 'success'
        });
      }
    },
    created () {
      this.$http.get(api.staffIntroduction).then((response) => {             // mark
        this.table = response.body.table;
        this.table2 = response.body.table2;
        this.message = response.body.message;
        console.log(this.table);
        console.log(this.table2);
      }, response => {
        // error callback
        this.$message({
          message: '',
          type: 'error'
        });
      });
    }
  };
</script>

<style lang="stylus-loader" rel="stylesheet/stylus">
  .staffIntroduction .title
    float:left
  .staffIntroduction .input
    margin-left:100px
  .staffIntroduction .table
    margin-top:50px
  .staffIntroduction .table2
    margin-top:50px
</style>


