<template>
  <div class="trashyDrug">
    <el-popover ref="popover5" placement="top" width="160" v-model="tipsVisible">
      <p></p>
      <div style="text-align: right; margin: 0">
        <el-button size="mini" type="text" @click="deleteAll(false)"></el-button>
        <el-button type="primary" size="mini" @click="deleteAll(true)"></el-button>
      </div>
    </el-popover>
    <div class="headerBt">
      <router-link to="addTrashy"><el-button size="small"></el-button></router-link>
      <el-button size="small" type="danger"  v-popover:popover5></el-button>
    </div>
    <el-table :data="trashyDrug">
      <el-table-column prop="name" label=""></el-table-column>
      <el-table-column prop="amount" label=""></el-table-column>
      <el-table-column prop="handlers" label=""></el-table-column>
      <el-table-column prop="trashy" label=""></el-table-column>
      <el-table-column prop="manufacturers" label=""></el-table-column>
      <el-table-column label="">
        <template scope="scope">
          <el-button
            size="small"
            @click="handleEdit(scope.$index, scope.row)"></el-button>
          <el-button
            size="small"
            type="danger"
            @click="handleDelete(scope.$index, scope.row)"></el-button>

        </template>
      </el-table-column>
    </el-table>
    <el-dialog title="" :visible.sync="dialogFormVisible">
      <el-form :model="editForm">
        <el-form-item label="">
          <el-input v-model.trim="editForm.name" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model.trim.number="editForm.amount" type="number"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model.trim="editForm.handlers"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model.trim="editForm.trashy"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model.trim="editForm.manufacturers"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false"> </el-button>
        <el-button type="primary" @click="toEditTrashy"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script type="text/ecmascript-6">
  import {api} from '../../../global/api.js';
  export default {
    data () {
      return {
        trashyDrug: [],
        tipsVisible: false,
        dialogFormVisible: false,
        editIndex: '',
        editForm: {
          id: '',
          name: '',
          manufacturers: '',
          amount: '',
          handlers: '',
          trashy: ''
        }
      };
    },
    created () {
      // mark
      this.$http.get(api.trashy).then((response) => {             // mark
        this.trashyDrug = response.body.trashyDrug;
        console.log(this.trashyDrug);
      }, response => {
        // error callback
        this.$message('');
      });
    },
    methods: {
      handleEdit (index, row) {
        this.editIndex = index;
        this.editForm = this.trashyDrug[index];
        this.dialogFormVisible = true;
      },
      handleDelete (index, row) {
        console.log(index, row);
        // mark post
        this.$message({
          message: 'id' + this.trashyDrug[index].id,
          type: 'success'
        });
        this.$http.get(api.Storage, {params: {id: this.trashyDrug[index].id}}).then(function (response) {
          this.trashyDrug.splice(index, 1);
        }, function () {
          this.$message.error(',');
        });
      },
      deleteAll (toDelete) {
        this.tipsVisible = false;
        if (toDelete) {
          this.$message('');
          // mark post
          this.$http.get(api.trashy).then(function (response) {
            this.trashyDrug = [];
          }, function () {
            this.$message.error(',');
          });
        }
      },
      toEditTrashy () {
        this.$http.get(api.trashy, {params: {form: this.editForm}}).then(function (response) {
          this.$message({
            message: '',
            type: 'success'
          });
          this.trashyDrug[this.editIndex] = this.editForm;
          this.dialogFormVisible = false;
        }, function () {
          this.$message.error(',');
        });
      }
    }
  };
</script>

<style lang="stylus-loader" rel="stylesheet/stylus">
  .trashyDrug
    .headerBt
      margin-bottom:20px
</style>



