<template>
  <div class="checkInfo">
    <div class="title">
        <p></p>
    </div>

    <el-table :data="table" height="480" border style="width: 100%">
    <el-table-column label="" width="100">
      <template scope="scope">{{ scope.$index }}</template>
    </el-table-column>
    <el-table-column
      prop="complainant"
      label=""
      width="180">
    </el-table-column>
    <el-table-column
      prop="type"
      label=""
      width="180">
    </el-table-column>
    <el-table-column
      prop="date"
      label="">
    </el-table-column>
    <el-table-column
      prop="content"
      label="">
    </el-table-column>
    <el-table-column
      prop="phone"
      label="">
    </el-table-column>
    <el-table-column align="center" label="">
      <template scope="scope">
        <el-button size="small" @click="dialogVisible = true"></el-button>
        <el-button size="small" type="danger" @click="handleDelete(scope.$index, scope.row)"></el-button>
      </template>
    </el-table-column>
  </el-table>

    <el-dialog title="" :visible.sync="dialogVisible" size="tiny">
      <el-input type="textarea" :rows="5" placeholder="" v-model="textarea"></el-input>
      <span slot="footer" class="dialog-footer">
      <el-button @click="dialogVisible = false"> </el-button>
      <el-button type="primary" @click="submit"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script type="text/ecmascript-6">
  import {api} from '../../../global/api.js';
  export default {
    data () {
      return {
        table: [],
        dialogVisible: false,
        textarea: ''
      };
    },
    methods: {
      handleDelete (index, row) {
        let vm = this;
        console.log('row: ', index, row);
        vm.table.splice(index, 1);
        this.$message({
          message: '',
          type: 'success'
        });
      },
      submit () {
        this.dialogVisible = false;
        console.log('', this.textarea);
        this.textarea = '';
        this.$message({
          message: '',
          type: 'success'
        });
      }
    },
    created () {
      this.$http.get(api.complainInfo).then((response) => {             // mark
        this.table = response.body.table;
        console.log(this.table);
      }, response => {
        // error callback
        this.$message({
          message: '',
          type: 'error'
        });
      });
    }
  };
</script>

<style lang="stylus-loader" rel="stylesheet/stylus">
  .checkInfo .title
    p
      font-size:24px
      text-align:center
      margin-bottom:50px
</style>


