<template>
  <div class="absenceRequest">
    <h1 style="text-align: center;font-size: 24px;
    margin-bottom: 25px;"></h1>
    <el-form :data="ruleForm" label-width="100px" class="demo-ruleForm" ref="ruleForm">
      <div>
        <h2 style="margin-bottom:20px;"></h2>
        <el-form-item label="" prop="organization">
          <el-input v-model="ruleForm.organization"></el-input>
        </el-form-item>
        <el-form-item label="/" prop="department">
          <el-input v-model="ruleForm.group"></el-input>
        </el-form-item>
        <el-form-item label="/" prop="jobs">
          <el-input v-model="ruleForm.group"></el-input>
        </el-form-item>
      </div>
      <div>
        <h2 style="margin-bottom:20px;">:</h2>
        <el-form-item label="" prop="name">
          <el-input v-model="ruleForm.name"></el-input>
        </el-form-item>
        <el-form-item label="" prop="age">
          <el-input v-model="ruleForm.age"></el-input>
        </el-form-item>
        <el-form-item label="" prop="employeeNumber">
          <el-input v-model="ruleForm.employeeNumber"></el-input>
        </el-form-item>
      </div>
      <div>
        <h2 style="margin-bottom:20px;">:</h2>
        <el-checkbox-group v-model="ruleForm.category">
          <el-checkbox label=""></el-checkbox>
          <el-checkbox label=""></el-checkbox>
          <el-checkbox label=""></el-checkbox>
          <el-checkbox label=""></el-checkbox>
          <el-checkbox label=""></el-checkbox>
          <el-checkbox label=""></el-checkbox>
          <el-checkbox label=""></el-checkbox>
          <el-checkbox label=""></el-checkbox>
        </el-checkbox-group>
      </div>
      <div>
        <h2 style="margin:20px 0px;">:</h2>
        <el-form-item label="" prop="reason">
          <el-input type="textarea" v-model="ruleForm.reason"></el-input>
        </el-form-item>
        <el-form-item label="" prop="absenceTime" class="demonstration">
          <el-date-picker v-model="ruleForm.absenceTime" type="daterange" placeholder="" :picker-options="pickerOptions">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="____" prop="totalTime">
          <el-input v-model="ruleForm.totalTime"></el-input>
        </el-form-item>
      </div>
      <div>
        <h2 style="margin-bottom:20px;">:</h2>
        <el-form-item label="" prop="departmentManager">
          <el-input v-model="ruleForm.departmentManager"></el-input>
        </el-form-item>
        <el-form-item label="" prop="director">
          <el-input v-model="ruleForm.director"></el-input>
        </el-form-item>
        <span>:     </span>
      </div>
      <div>
        <el-form-item class="center">
          <el-button type="primary" @click="onSubmit"></el-button>
          <!-- <el-button @click="resetForm('ruleForm')"></el-button> -->
          <el-button @click="offReset"></el-button>
        </el-form-item>
      </div>
    </el-form>
  </div>
</template>
<script type="text/ecmascript-6">
export default {
  data () {
    return {
      ruleForm: {
        organization: '',
        department: '',
        jobs: '',
        name: '',
        age: '',
        employeeNumber: '',
        category: [],
        reason: '',
        absenceTime: '',
        totalTime: '',
        departmentManager: '',
        director: ''
      }
    };
  },
  methods: {
    onSubmit () {
      // let me = this;
      // this.$http.post(api.addPatient).then(function (response) {
      //   console.log('json', response.ruleForm);
      //   me.ruleForm = me.data.ruleForm;
      // }, function (response) {
      //   alert('');
      // });
      console.log('', JSON.stringify(this.ruleForm));
    },
    offReset () {
      this.ruleForm = {
        organization: '',
        group: '',
        jobs: '',
        name: '',
        age: '',
        employeeNumber: '',
        checkList: [],
        reason: '',
        absenceTime: '',
        totalTime: '',
        departmentManager: '',
        director: ''
      };
    },
    pickerOptions: {
      disabledDate (time) {
        return time.getTime() < Date.now - 8.64e7;
      }
    }

    // resetForm (formName) {
    //   this.$refs[formName].resetFields();
    // }
  }
};
</script>
<style type="text/css">
.absenceRequest .el-input, .absenceRequest .el-input__inner {
  width: 170px; 
  display: inline-block;
}
.absenceRequest .el-form-item {
  margin-bottom: 22px;
  margin-right: 50px;
  display: inline-block;
}
.absenceRequest .el-checkbox, .absenceRequest .el-checkbox__input {
    cursor: pointer;
    display: inline-block;
    position: relative;
    white-space: nowrap;
    margin-left: 29px;
}
.absenceRequest .center{
  padding-left: 300px;
  margin-top: 50px;
}
</style>


