<template>
  <div class="shift">
    <div class="title">
      <p></p>
    </div>
    <div class="filter-container">
      <el-input @keyup.enter.native="handleFilter" style="width: 200px;" class="filter-item" placeholder="" v-model="searchName"></el-input>
      <el-input @keyup.enter.native="handleFilter" style="width: 200px;" class="filter-item" placeholder="" v-model="searchDepartment"></el-input>
      <!-- <el-select clearable class="filter-item" style="width: 130px" placeholder="" v-model="searchDepartment">
        <el-option v-for="item in typeOptions" :key="item.key" :label="item.display_name+'('+item.key+')'" :value="item.key">
        </el-option>
      </el-select> -->
      <el-button class="filter-item" type="primary" icon="search" @click="handelSearch"></el-button>
      <el-button  type="primary" icon="" @click="getAll"></el-button>
      <el-button class="filter-item" type="primary" @click="handelCreate"  icon="edit"></el-button>
      <el-button class="filter-item" type="primary" @click="handleDelAll"  icon="edit"></el-button>
      <el-button class="filter-item" type="primary" icon="document" @click="handleDownload"></el-button>
    </div>

    <el-table v-if="table" :data="table" border style="width: 100%" height="400">
    <el-table-column label="" width="120" fixed>
      <template scope="scope">{{ scope.$index }}</template>
    </el-table-column>
    <el-table-column prop="date" fixed label="" width="120"></el-table-column>
    <el-table-column prop="time" label="" width="200"></el-table-column>
    <el-table-column prop="name" label="" width="150"></el-table-column>
    <el-table-column prop="sex"  label="" width="150"></el-table-column>
    <el-table-column prop="department" label="" width="150"></el-table-column>
    <el-table-column prop="phone" label="" width="150"></el-table-column>
    <el-table-column prop="attendence" label="" width="120" v-model="listQuery.type"></el-table-column>
    <el-table-column prop="signature" label="" width="120"></el-table-column>
    <el-table-column align="center" label="">
      <template scope="scope">
        <el-button size="small" @click="handleEdit(scope.$index)"></el-button>
        <el-button size="small" type="danger" @click="handleDelete(scope.$index, scope.row)"></el-button>
      </template>
    </el-table-column>
  </el-table>
 <!--   -->
  <el-dialog title="" :visible.sync="dialogFormVisible">
        <el-form class="small-space" :model="form" label-position="left" label-width="70px" style='width: 400px; margin-left:50px;'>
          <el-form-item label="">
            <el-input v-model="form.name"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="form.date"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-select v-model="form.time" placeholder="">
              <el-option label=" 800-1600" value=" 800-1600"></el-option>
              <el-option label=" 800-1700" value=" 800-1700"></el-option>
              <el-option label=" 1600-2400" value=" 1600-2400"></el-option>
              <el-option label=" 2400-800" value=" 2400-800"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="">
            <el-radio-group v-model="form.sex">
              <el-radio label=""></el-radio>
              <el-radio label=""></el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="form.department"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="form.phone"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="form.signature"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogFormVisible = false"> </el-button>          
          <el-button type="primary" @click="handleCreateSubmit"> </el-button>
          <el-button type="primary" @click="resetForm('form')"></el-button>
        </div>
      </el-dialog>
      <!--  -->
      <el-dialog title="" :visible.sync="dialogFormEditVisible">
        <el-form class="small-space" :model="form" label-position="left" label-width="70px" style='width: 400px; margin-left:50px;'>
          <el-form-item label="">
            <el-input v-model="tableEdit.name" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="tableEdit.date" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-select v-model="tableEdit.time" placeholder="">
              <el-option label=" 800-1600" value=" 800-1600"></el-option>
              <el-option label=" 800-1700" value=" 800-1700"></el-option>
              <el-option label=" 1600-2400" value=" 1600-2400"></el-option>
              <el-option label=" 2400-800" value=" 2400-800"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="">
            <el-radio-group v-model="tableEdit.sex">
              <el-radio label=""></el-radio>
              <el-radio label=""></el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="tableEdit.department" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="tableEdit.phone" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-select v-model="tableEdit.attendence" placeholder="">
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="tableEdit.signature" placeholder=""></el-input>
          </el-form-item>
        </el-form>
         <div slot="footer" class="dialog-footer">
          <el-button @click="dialogFormEditVisible = false"> </el-button>          
          <el-button type="primary" @click="handleEditSubmit"> </el-button>
         </div>
      </el-dialog>
  </div>
</template>

<script type="text/ecmascript-6">
  import {api} from '../../../global/api.js';
  export default {
    data () {
      return {
        table: [],
        tableEdit: [],
        searchList: [],
        searchName: '',
        searchDepartment: '',
        listQuery: {
          type: null
        },
        dialogFormVisible: false,
        dialogFormEditVisible: false,
        form: {
          name: '',
          date: '',
          time: '',
          phone: '',
          attendence: '',
          department: '',
          sex: '',
          signature: ''
        },
        typeOptions: [
          {key: '001', display_name: ''},
          {key: '002', display_name: ''},
          {key: '003', display_name: ''},
          {key: '004', display_name: ''},
          {key: '005', display_name: ''},
          {key: '006', display_name: ''},
          {key: '007', display_name: ''},
          {key: '008', display_name: ''}
        ]
      };
    },
    methods: {
      // 
      handleEdit (index) {
        this.dialogFormEditVisible = true;
        this.Index = index;
        this.tableEdit = this.table[index];
      },
      handleEditSubmit (index) {
        this.dialogFormEditVisible = false;
        let oldDate = this.tableEdit.date;
        let year = new Date(oldDate).getFullYear();
        let month = new Date(oldDate).getMonth() + 1;
        let day = new Date(oldDate).getDate();
        let dateFormat = year + '-' + month + '-' + day;
        this.tableEdit.date = dateFormat;
        console.log(dateFormat);
        this.table[index] = this.tableEdit;
        this.$message({
          message: '',
          type: 'success'
        });
      },
      // 
      handelSearch () {
        let intendedSearch = this;
        intendedSearch.$http.get(api.table_shift, {params: {name: intendedSearch.searchName, department: intendedSearch.searchDepartment}}).then((response) => {
          intendedSearch.table = [];
          for (let i = 0; i < response.data.table.length; i++) {
            if (response.data.table[i].name === intendedSearch.searchName && response.data.table[i].department === intendedSearch.searchDepartment) {
              intendedSearch.table.push(response.data.table[i]);
            } else if (response.data.table[i].name === intendedSearch.searchName) {
              intendedSearch.table.push(response.data.table[i]);
            } else if (response.data.table[i].department === intendedSearch.searchDepartment) {
              intendedSearch.table.push(response.data.table[i]);
            }
          };
        }, response => {
            // error callback
          intendedSearch.$notify.error({
            message: ''
          });
        });
      },
      // 
      getAll () {
        this.table = this.searchList;
        this.searchName = '';
      },
      // 
      handleDownload () {
        var vm = this;
        require.ensure([], () => {
          const { export_json_to_excel } = require('vendor/Export2Excel');
          const tHeader = ['', '', '', '', '', '', '', ''];
          const filterVal = ['date', 'time', 'name', 'sex', 'department', 'phone', 'attendence', 'signature'];
          const table = vm.table;
          const data = vm.formatJson(filterVal, table);
          export_json_to_excel(tHeader, data, 'excel');
        });
      },
      formatJson (filterVal, jsonData) {
        return jsonData.map(v => filterVal.map(j => v[j]));
      },
      // 
      handleDelete (index, row) {
        let vm = this;
        console.log('row: ', index, '-----', row);
        vm.table.splice(index, 1);
        this.$message({
          message: '',
          type: 'success'
        });
      },
      // 
      handleDelAll () {
        this.table = [];
      },
      // 
      handelCreate () {
        this.dialogFormVisible = true;
        this.formClear();
      },
      // 
      handleCreateSubmit () {
        let vm = this;
        console.log('', vm.form, vm.table);
        vm.table.push(vm.form);
        this.dialogFormVisible = false;
        this.$message({
          message: '',
          type: 'success'
        });
      },
      // 
      formClear () {
        this.form = {
          name: '',
          time: '',
          phone: '',
          attendence: '',
          department: '',
          sex: '',
          signature: ''
        };
      },
      // 
      resetForm () {
        this.form = {
          name: '',
          time: '',
          phone: '',
          attendence: '',
          department: '',
          sex: '',
          signature: ''
        };
      }
    },
    created () {
      this.$http.get(api.table_shift).then((response) => {             // mark
        this.table = response.body.table;
        this.searchList = response.body.table;
        console.log(this.table);
      }, response => {
        // error callback
        this.$message({
          message: '',
          type: 'error'
        });
      });
    }
  };
</script>

<style lang="stylus-loader" rel="stylesheet/stylus">
  .shift .title
    p
      font-size:24px
      text-align:center
      margin-bottom:50px
  .shift .filter-container
      margin-bottom:20px
</style>


