<template>
  <div class="addPatient">
    <el-form :model="ruleForm" class="demo-ruleForm" ref="ruleForm" :rules="rules" label-width="100px" >
      <h1 class="record">
        :
        <span class="demonstration"></span>
        <el-date-picker v-model="ruleForm.date" type="date" placeholder="" :picker-options="pickerOptions" prop="date"></el-date-picker>
      </h1>
      <br><br>
      <div>
        <el-form-item label="" prop="name">
          <el-input v-model="ruleForm.name"></el-input>
        </el-form-item>
        <el-form-item label="" prop="region">
          <el-select v-model="ruleForm.region" placeholder="">
            <el-option label="" value="guahao"></el-option>
            <el-option label="" value="menzhen"></el-option>
            <el-option label="" value="jizhen"></el-option>
            <el-option label="" value="huayan"></el-option>
            <el-option label="" value="shoushu"></el-option>
            <el-option label="" value="huayan"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="idCard">
          <el-input v-model="ruleForm.idCard"></el-input>
        </el-form-item>
      </div>
      <div>
        <el-form-item label="" prop="age">
          <el-input v-model.number="ruleForm.age"></el-input>
        </el-form-item>
        <el-form-item label="" prop="phone">
          <el-input v-model="ruleForm.phone"></el-input> 
        </el-form-item>
        <el-form-item label="" prop="sex">
          <el-radio-group v-model="ruleForm.sex">
            <el-radio :label="1" label-width="150px"></el-radio>
            <el-radio :label="2" label-width="150px"></el-radio>
          </el-radio-group>
        </el-form-item>
      </div>
      <div>
        <el-form-item label="">
          <el-select v-model="selectProvince1" placeholder="">
            <el-option v-for="item in ruleForm.provinces" :key="item.value" :label="item.label" :value="item.value">
            </el-option>
          </el-select>
          <el-select v-model="selectCity1" placeholder="">
            <el-option v-for="item in ruleForm.citys" :key="item.value" :label="item.label" :value="item.value"></el-option>
          </el-select>
          <el-input v-model="ruleForm.moreAddress" placeholder="" label-width:200px></el-input>
        </el-form-item>
      </div>
      <br>
      <h3></h3><br>
      <div>
        <el-form-item label="" prop="Name">
          <el-input v-model="ruleForm.Name"></el-input>
        </el-form-item>
        <el-form-item label="" prop="Phone">
          <el-input v-model.number="ruleForm.Phone"></el-input>
        </el-form-item>
        <el-form-item label="" prop="relation">
          <el-select v-model="ruleForm.relation" placeholder="">
            <el-option label="" value="1"></el-option>
            <el-option label="" value="2"></el-option>
            <el-option label="" value="3"></el-option>
            <el-option label="" value="4"></el-option>
            <el-option label="" value="5"></el-option>
            <el-option label="" value="6"></el-option>
            <el-option label="" value="7"></el-option>
            <el-option label="" value="8"></el-option>
            <el-option label="" value="9"></el-option>
            <el-option label="" value="10"></el-option>
          </el-select>
        </el-form-item>
      </div>
      <div>
        <el-form-item label="" prop="symptoms">
          <el-input type="textarea" v-model="ruleForm.symptoms"></el-input>
        </el-form-item>
      </div>
      <div>
        <h3></h3><br>
        <el-form-item label="" prop="illHistory">
          <el-input type="textarea" v-model="ruleForm.illHistory"></el-input>
        </el-form-item>
      </div>
      <div>
        <h3></h3>
        <el-form-item label="" prop="doctor">
          <el-input v-model="ruleForm.doctor"></el-input>
        </el-form-item>
        <el-form-item label="" prop="department">
          <el-select v-model="ruleForm.department" placeholder="">
            <el-option label="" value="1"></el-option>
            <el-option label="" value="2"></el-option>
            <el-option label="" value="3"></el-option>
            <el-option label="" value="4"></el-option>
            <el-option label="" value="5"></el-option>
            <el-option label="" value="6"></el-option>
          </el-select>
        </el-form-item>
      </div>
      <div>
        <el-form-item class="center">
          <el-button type="primary" @click="onSubmit"></el-button>
          <el-button @click="offReset('ruleForm')"></el-button>
        </el-form-item>
      </div>
    </el-form>
  </div>
</template>
<script type="text/ecmascript-6">
import {api} from '../../../global/api.js';
export default {
  data () {
    let checkAge = (rule, value, callback) => {
      if (!value) {
        return callback(new Error(''));
      };
      setTimeout(() => {
        if (/^\d+$/.test(value) === false) {
          callback(new Error(''));
        } else {
          callback();
        }
      }, 300);
    };
    var checkName = (rule, value, callback) => {
      if (!value) {
        return callback(new Error(''));
      } else {
        callback();
      }
    };
    var checkPhone = (rule, value, callback) => {
      if (!value) {
        return callback(new Error(''));
      };
      setTimeout(() => {
        if (/^\d+$/.test(value) === false) {
          callback(new Error(''));
        } else if (value.length > 11) {
          callback(new Error(''));
        } else {
          callback();
        }
      }, 500);
    };
    var checkidCard = (rule, value, callback) => {
      if (!value) {
        return callback(new Error(''));
      };
      setTimeout(() => {
        if (/^(\w){6,20}$/.test(value) === false) {
          callback(new Error(''));
        } else {
          callback();
        }
      }, 500);
    };
    return {
      ruleForm: {
        date: '',
        name: '',
        region: '',
        idCard: '',
        sex: 2,
        age: '',
        phone: '',
        Name: '',
        Phone: '',
        relation: '',
        address: '',
        provinces: [],
        citys: [],
        moreAddress: '',
        symptoms: '',
        doctor: '',
        department: ''
      },
      selectProvince1: '',
      selectCity1: '',
      pickerOptions: {
        disabledDate (time) {
          return time.getTime() < Date.now - 8.64e7;
        }
      },
      rules: {
        name: [
          { required: true, validator: checkName, trigger: 'blur' }
        ],
        age: [
          { required: true, validator: checkAge, trigger: 'blur' }
        ],
        phone: [
          { required: true, validator: checkPhone, trigger: 'blur' }
        ],
        region: [
          { required: true, trigger: 'blur' }
        ],
        idCard: [
          { required: true, validator: checkidCard, trigger: 'blur' }
        ],
        sex: [
          { required: true, trigger: 'blur' }
        ]
      }
    };
  },
  mounted () {
    let me = this;
    me.$http.get(api.provinces).then(function (response) {
      me.ruleForm.provinces = response.data.provinces;
    }, function (response) {
      alert('');
    });
  },
  methods: {
    onSubmit () {
      // let me = this;
      // this.$http.post('../../static/patient/addPatient.json').then(function (response) {
      //   console.log('json', response.ruleForm);
      //   me.ruleForm = me.data.ruleForm;
      // }, function (response) {
      //   alert('');
      // });
      console.log('', JSON.stringify(this.ruleForm));
    },
    offReset (form) {
      this.$refs[form].resetFields();
      this.ruleForm.date = '';
      this.ruleForm.moreAddress = '';
      this.selectProvince1 = '';
      this.selectCity1 = '';
    }
  },
  watch: {
    selectProvince1: function () {
      // console.log(this.selectProvince1);
      for (let i = 0; i < this.ruleForm.provinces.length; i++) {
        if (this.selectProvince1 === this.ruleForm.provinces[i].value) {
          this.ruleForm.citys = this.ruleForm.provinces[i].citys;
        }
      }
    }
  }
};
</script>
<!-- <script src="https://unpkg.com/vue"></script> -->
<style type="text/css">
  .addPatient .el-input, .addPatient .el-input__inner {
    display: inline-block;
    width: 192px;
  }
  .addPatient .el-form-item{
    padding-right: 30px;
  }
  .addPatient .el-form{
    padding-bottom: 20px;
  }
  .addPatient .el-textarea__inner{
    width: 500px;
  }
  .addPatient .center{
    padding-left: 200px;
  }
  .addPatient .demonstration{
    color: #333;
    font-size: 24px;
  }
  .addPatient .record{
    font-size: 28px;
  }
  .addPatient .el-form-item{
    display: inline-block;
  }
</style>



