<template>
  <div class="takecash">
    <p class="takecash-title"></p>
    <!--  -->
    <el-form :model="takecash" :rules="rules" ref="takecashForm" label-width="100px" class="takecash-ruleForm">
      <el-form-item label="" prop="patientName">
        <el-input v-model="takecash.patientName"></el-input>
      </el-form-item>
      <el-form-item label="" prop="certificate_value">
        <el-select v-model="takecash.certificate_value" placeholder="">
          <el-option label="" value="idCard"></el-option>
          <el-option label="" value="healthCard"></el-option>
        </el-select>
      </el-form-item>
      <p class="takecash-isshow" v-if="takecash.certificate_value === 'idCard'">
        <el-form-item label="" prop="cardNumber">
          <el-input v-model="takecash.cardNumber"></el-input>
        </el-form-item>
      </p>
      <p class="takecash-isshow" v-else-if="takecash.certificate_value === 'healthCard'">
        <el-form-item label="" prop="cardNumber">
          <el-input v-model="takecash.cardNumber"></el-input>
        </el-form-item>
      </p>
      <p v-else></p>
      <el-form-item label="" prop="prescript_number">
        <el-input v-model="number"></el-input>
      </el-form-item>
      <!-- numberisShowisShow -->
      <!-- -->
      <template  v-if="number === isShow && number !== ''">
        <el-table :data="takecash.prescription">
          <el-table-column prop="sort" label="">
          </el-table-column>
          <el-table-column prop="drugname" label="">
          </el-table-column>
          <el-table-column prop="quantity" label="/">
          </el-table-column>
          <el-table-column prop="usage" label="">
          </el-table-column>                
        </el-table>
      </template>
      <template v-else>
      </template>
      <p class="takecash_p">
        <el-form-item label="" class="shouldMoney" label-width="100">
          <span v-show="showMoney" class="money">{{ takecash.shouldMoney }}</span>
        </el-form-item>
        <el-button type="primary" class="computeBtn" @click="compute()"></el-button>
      </p>
      <el-form-item label="" prop="exactMoney" class="exactMoney" label-width="80">
        <el-input v-model="takecash.exactMoney" style="width: 75%"></el-input>
      </el-form-item>
      <p class="takecash_p">
        <el-form-item label="" class="theChange" label-width="100">
          <span v-show="showChange" class="money">{{ takecash.theChange }}</span>
        </el-form-item>
        <el-button type="primary" class="changeBtn" @click="change()"></el-button>
      </p>
      <el-button type="reset" @click="resetForm('takecashForm')" class="reset"></el-button>
    </el-form>
  </div>
</template>

<script type="text/ecmascript-6">
  import {api} from '../../../global/api.js';
  export default {
    data () {
      // 
      let checkName = (rule, value, callback) => {
        for (let k = 0; k < this.patients.length; k++) {
          if (value !== this.patients[k].name) {
            callback(new Error(''));
          } else {
            callback();
          }
        }
      };
      // 
      let checkPrescription = (rule, value, callback) => {
        for (let k = 0; k < this.patients.length; k++) {
          if (value !== this.patients[k].number) {
            callback(new Error(''));
          } else {
            callback();
          }
        }
      };
      // 
      let checkexactMoney = (rule, value, callback) => {
        if (/^\d+$/.test(value) === false) {
          callback(new Error(''));
        } else {
          if ((value - this.takecash.shouldMoney) < 0) {
            callback(new Error(''));
          } else {
            callback();
          }
        }
      };
      // 
      let checkCertificate = (rule, value, callback) => {
        if (/^\d+$/.test(value) !== false) {
          if (value.length !== 18) {
            callback(new Error('18'));
          } else {
            for (let k = 0; k < this.patients.length; k++) {
              if (value === this.patients[k].idCard) {
                if (this.patients[k].name === this.takecash.patientName) {
                  callback();
                } else {
                  callback(new Error(''));
                }
              } else {
                callback(new Error(''));
              }
            }
          }
        } else {
          callback(new Error(''));
        }
      };
      return {
        takecash: {
          patientName: '',
          certificate_value: '',
          cardNumber: '',
          prescript_number: '',
          prescription: [],
          shouldMoney: '',
          exactMoney: '',
          theChange: ''
        },
        number: '',   //  
        isShow: '',   // 
        showMoney: false,
        showChange: false,
        patients: [],  // 
        rules: {
          patientName: [
            {required: true, validator: checkName, trigger: 'blur'}
          ],
          certificate_value: [
            { required: true, message: '', trigger: 'change' }
          ],
          cardNumber: [
            {required: true, validator: checkCertificate, trigger: 'blur'}
          ],
          prescript_number: [
            {required: true, validator: checkPrescription, trigger: 'blur'}
          ],
          exactMoney: [
            {required: true, validator: checkexactMoney, trigger: 'blur'}
          ]
        }
      };
    },
    created () {
      let takecashThis = this;
      takecashThis.$http.get(api.patientList).then((response) => {
        // json
        takecashThis.patients = response.data.tableData;
      }, response => {
        // error callback
        takecashThis.$notify.error({
          message: ''
        });
      });
    },
    methods: {
      // 
      compute () {
        this.showMoney = true;
        this.$refs.takecashForm.validate((valid) => {
          if (!valid) {
            this.$notify.error({
              message: ''
            });
          }
        });
      },
      // 
      resetForm (formName) {
        this.$refs.takecashForm.resetFields();
        this.isShow = '';
        this.number = '';
        this.showMoney = false;
        this.showChange = false;
      },
      // 
      change () {
        this.$refs.takecashForm.validate((valid) => {
          if (valid) {
            if ((this.takecash.exactMoney - this.takecash.shouldMoney) < 0) {
              this.takecash.theChange = '';
              this.showChange = true;
            } else {
              if (this.takecash.shouldMoney === '') {
                this.showChange = false;
              } else {
                this.takecash.theChange = this.takecash.exactMoney - this.takecash.shouldMoney;
                this.showChange = true;
              }
            }
          } else {
            this.$notify.error({
              message: ''
            });
          }
        });
      }
    },
    // number
    // prescription[]
    // this.isShow
    watch: {
      number: function () {
        this.takecash.prescript_number = this.number;

        for (let i = 0; i < this.patients.length; i++) {
          if (this.patients[i].number === this.number) {
            this.takecash.prescription = this.patients[i].prescription.drug;
            this.isShow = this.patients[i].number;
            // 
            this.takecash.shouldMoney = 320;
          }
        }
      }
    }
  };
</script>

<style lang="stylus-loader" rel="stylesheet/stylus">
  .takecash 
    .takecash-title
      font-weight: bold
      font-size: 28px
      width: 50%
      margin: 0 auto
      text-align: center
      padding-bottom: 40px
    .takecash-ruleForm
      width: 50%
      margin: 0 auto
      .el-form-item
        width: 100%
        .el-select
          width: 100%
      .takecash_p
        overflow: hidden
        padding-left: 30px
        padding-top: 20px
        .shouldMoney
          float: left
          display: inline-block
          width:80%
          /*top: 325px*/
          /*right: 335px*/
        .theChange
          float: left
          width:81%
          margin-left: 25px
          display: inline-block
        .computeBtn
          float: left
          display: inline-block
        .changeBtn
          display: inline-block
          float: left
          /*top: 460px*/
          /*right: 335px*/
      .exactMoney
          margin-left: 20px
          width: 100%
        .money
          font-size: 20px
          color: black
          font-weight: bold
      .reset
        margin-left: 45%
        margin-top: 50px
        
</style>



