<template>
  <div class="putInStorage">
    <el-form ref="form" :model="form" label-width="80px"  :rules="rules">
      <el-form-item label="" prop="name">
        <el-input v-model.trim="form.name"></el-input>
      </el-form-item>
      <el-form-item label="" prop="amount">
        <el-input v-model.trim.number="form.amount" type="number"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-col :span="11">
          <el-date-picker type="date" placeholder="" v-model="date1" style="width: 100%;"></el-date-picker>
        </el-col>
        <el-col class="line" :span="2">-</el-col>
        <el-col :span="11">
          <el-time-picker type="fixed-time" placeholder="" v-model="form.date2" style="width: 100%;"></el-time-picker>
        </el-col>
      </el-form-item>
      <el-form-item label="" prop="manufacturers">
        <el-input v-model.trim="form.manufacturers" ></el-input>
      </el-form-item>
      <el-form-item label="" prop="species">
        <el-input v-model.trim="form.species"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-date-picker readOnly="true" type="date" placeholder="" v-model="form.productionData" style="width: 100%;"></el-date-picker>
      </el-form-item>
      <el-form-item label=""  prop="specification">
        <el-input v-model.trim="form.specification"></el-input>
      </el-form-item>
      <el-form-item label=""  prop="dosageForm">
        <el-input v-model.trim="form.dosageForm"></el-input>
      </el-form-item>
      <el-form-item label=""  prop="unitPrice">
        <el-input v-model.trim.number="form.unitPrice" type="number"></el-input>
      </el-form-item>
      <el-form-item label="">
        {{form.totalPrices}}
      </el-form-item>
      <el-form-item label=""  prop="temperature">
        <el-select v-model.trim="form.temperature" placeholder="">
          <el-option label="" value="01"></el-option>
          <el-option label="" value="02"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label=""  prop="handlers">
        <el-input v-model.trim="form.handlers"></el-input>
      </el-form-item>
      <el-form-item label=""  prop="qualityOfficer">
        <el-input v-model.trim="form.qualityOfficer"></el-input>
      </el-form-item>
      <el-form-item label=""  prop="warehouseman">
        <el-input v-model.trim="form.warehouseman"></el-input>
      </el-form-item>
      <el-form-item label=""  prop="pattern">
        <el-select v-model.trim="form.pattern" placeholder="">
          <el-option label="" value="01"></el-option>
          <el-option label="" value="02"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="submitForm('form')"></el-button>
        <el-button @click="resetForm('form')"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script type="text/ecmascript-6">
  import {api} from '../../../global/api.js';
  export default {
    data () {
      return {
        date1: '',
        form: {
          name: '',
          manufacturers: '',
          amount: '',
          unitPrice: '',
          totalPrices: 0,
          temperature: '',
          handlers: '',
          qualityOfficer: '',
          warehouseman: '',
          pattern: '',
          date2: '',
          species: '',
          productionData: '',
          specification: '',
          dosageForm: ''
        },
        rules: {
          name: [
            { required: true, message: '', trigger: 'blur' }
          ],
          manufacturers: [
            { required: true, message: '', trigger: 'blur' }
          ],
          species: [
            { required: true, message: '', trigger: 'blur' }
          ],
          specification: [
            { required: true, message: '', trigger: 'blur' }
          ],
          dosageForm: [
            { required: true, message: '', trigger: 'blur' }
          ],
          handlers: [
            { required: true, message: '', trigger: 'blur' }
          ],
          qualityOfficer: [
            { required: true, message: '', trigger: 'blur' }
          ],
          warehouseman: [
            { required: true, message: '', trigger: 'blur' }
          ],
          pattern: [
            { required: true, message: '', trigger: 'blur' }
          ]
        }
      };
    },
    methods: {
      // vue-resource
      submitForm () { // mark
        if (!this.form.name || !this.form.manufacturers || !this.form.amount || !this.form.unitPrice || !this.form.temperature || !this.form.handlers || !this.form.qualityOfficer || !this.form.warehouseman || !this.form.pattern || !this.form.date2 || !this.form.species || !this.form.productionData || !this.form.specification || !this.form.dosageForm) {
          this.$message.error('');
        } else {
          this.form.date2 = this.form.date2.getFullYear() + '-' + this.form.date2.getMonth() + '-' + this.form.date2.getDay() + ' ' + this.form.date2.getHours() + ':' + this.form.date2.getMinutes() + ':' + this.form.date2.getSeconds();
          this.form.productionData = this.form.productionData.getFullYear() + '-' + this.form.productionData.getMonth();
          console.log(this.form);
          this.$message({
            message: '' + JSON.stringify(this.form),
            type: 'success'
          });
          this.$http.get(api.putInStorage, {params: {form: this.form}}).then(function (response) {
            console.log(response.body);
            this.resetForm();
          }, function () {
            this.$message.error('POST ,');
            this.resetForm();
          });
        }
      },
      resetForm () {
        this.date1 = '';
        this.form = {
          name: '',
          manufacturers: '',
          amount: '',
          unitPrice: '',
          totalPrices: 0,
          temperature: '',
          handlers: '',
          qualityOfficer: '',
          warehouseman: '',
          pattern: '',
          date1: '',
          date2: '',
          species: '',
          productionData: '',
          specification: '',
          dosageForm: ''
        };
      }
    },
    computed: {
      Amount () {
        return this.form.amount;
      },
      UnitPrice () {
        return this.form.unitPrice;
      }
    },
    watch: {
      Amount (newValue, oldValue) {
        this.form.totalPrices = this.form.amount * this.form.unitPrice;
      },
      UnitPrice (newValue, oldValue) {
        this.form.totalPrices = this.form.amount * this.form.unitPrice;
      }
    }
  };
</script>

<style lang="stylus-loader" rel="stylesheet/stylus">
  .putInStorage
    max-width:800px
</style>



