<template>
  <div>
    <h3 style="margin-bottom:20px;"></h3>
    <el-table :data="tableData" border style="width:100%;">
      <el-table-column fixed label="" prop="date" width="150"></el-table-column>
      <el-table-column label="
" prop="phone" width="150"></el-table-column>
      <el-table-column label="" prop="name" width="120"></el-table-column>
      <el-table-column label="" prop="content" width="350"></el-table-column>
      <el-table-column label="/
" prop="department" width="150"></el-table-column>
      <el-table-column label="" prop="way" width="100"></el-table-column>
      <el-table-column label="" prop="Processing" width="200"></el-table-column>
      <el-table-column label="" fixed="right" prop="results" width="250"></el-table-column>
    </el-table>
  </div>
</template>
<script type="text/ecmascript-6">
import Vue from 'vue';
import {api} from '../../../global/api.js';
export default {
  data () {
    return {
      tableData: []
    };
  },
  created () {
    let me = this;
    Vue.http.get(api.complaints).then(function (response) {
      console.log(response);
      console.log(':', response.tableData);
      me.tableData = response.data.tableData;
    }, function (response) {
      alert('');
    });
  }
  // methods: {
