<template>
  <div class="drug">
    <el-form :inline="true" class="demo-form-inline">
      <el-form-item label="">
        <el-input v-model="selectInformation" placeholder=""></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="selectFn"></el-button>
      </el-form-item>
      <p class="tips">
        <i>:01-02-</i><br>
        <i>01-02-</i>
      </p>
    </el-form>
    <template v-if="selectArr.length">
      <h2 class="title"></h2>
      <el-table :data="selectArr" height="450">
        <el-table-column  type="index" width="50"></el-table-column>
        <el-table-column  prop="name" label=""></el-table-column>
        <el-table-column  prop="amount" label=""></el-table-column>
        <el-table-column  prop="date2" label=""></el-table-column>
        <el-table-column  prop="manufacturers" label=""></el-table-column>
        <el-table-column  prop="species" label=""></el-table-column>
        <el-table-column  prop="productionData" label=""></el-table-column>
        <el-table-column  prop="specification" label=""></el-table-column>
        <el-table-column  prop="dosageForm" label=""></el-table-column>
        <el-table-column  prop="unitPrice" label=""></el-table-column>
        <el-table-column  prop="temperature" label=""></el-table-column>
        <el-table-column  prop="handlers" label=""></el-table-column>
        <el-table-column  prop="qualityOfficer" label=""></el-table-column>
        <el-table-column  prop="warehouseman" label=""></el-table-column>
        <el-table-column  prop="pattern" label=""></el-table-column>s
      </el-table>
    </template>
    <template>
      <h2 class="title"></h2>
      <el-table :data="drug">
      <el-table-column type="index" width="50"></el-table-column>
      <el-table-column  prop="name" label=""></el-table-column>
      <el-table-column  prop="amount" label=""></el-table-column>
      <el-table-column  prop="date2" label=""></el-table-column>
      <el-table-column  prop="manufacturers" label=""></el-table-column>
      <el-table-column  prop="species" label=""></el-table-column>
      <el-table-column  prop="productionData" label=""></el-table-column>
      <el-table-column  prop="specification" label=""></el-table-column>
      <el-table-column  prop="dosageForm" label=""></el-table-column>
      <el-table-column  prop="unitPrice" label=""></el-table-column>
      <el-table-column  prop="temperature" label=""></el-table-column>
      <el-table-column  prop="handlers" label=""></el-table-column>
      <el-table-column  prop="qualityOfficer" label=""></el-table-column>
      <el-table-column  prop="warehouseman" label=""></el-table-column>
      <el-table-column  prop="pattern" label=""></el-table-column>s
    </el-table>
      <div class="pageBlock">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="currentPage"
          :page-sizes="[10, 20, 100, 200]"
          :page-size="10"
          layout="total, sizes, prev, pager, next, jumper"
          :total=totalInformation>
        </el-pagination>
      </div>
  </template>
  </div>
</template>
<script type="text/ecmascript-6">
  import {api} from '../../../global/api.js';
  export default {
    data () {
      return {
        temperatures: ['', ''],
        currentPage: 4,
        nowpage: 1,
        handleSize: 10,
        selectInformation: '',
        drug: [],
        selectArr: [],
        totalInformation: 0
      };
    },
    created () {
      // 
      this.$http.get(api.Storage).then((response) => {             // mark
        this.drug = response.body.drug;
        // 
        this.totalInformation = response.body.total;
      }, response => {
        this.$message.error('');
      });
    },
    methods: {
      selectFn () {
        this.$message({
          message: ' ' + this.selectInformation,
          type: 'success'
        });
        this.$http.get(api.Storage, {params: {selectInformation: this.selectInformation}}).then(function (response) {
          this.selectArr = response.body.drug;
        }, function () {
          this.$message.error(',');
        });
      },
      handleSizeChange (val) {
        this.handleSize = val;
        console.log(this.handleSize, ` ${val} `);
      },
      handleCurrentChange (val) {
        console.log(`:`, val);
        const showindex = [val, this.handleSize, this.drug[this.drug.length - 1].id];
        this.$message({
          message: 'id ' + showindex,
          type: 'success'
        });
        this.$http.get(api.Storage, {params: {showindex: showindex}}).then(function (response) {
          this.drug = response.body.drug;
          // 
          this.totalInformation = response.body.total;
        }, function () {
//          this.$message.error(',');
        });
      }
    }
  };
</script>

<style lang="stylus-loader" rel="stylesheet/stylus">
.drug
  .title
    padding:10px 0
    margin:30px 0 10px 0
  .el-table
    margin-bottom:30px
  .pageBlock
    text-align: center
  .tips
    text-align: left
    float:right
    font-size:13px
    line-height:15px
    padding-top:60px
</style>



