<template>
  <div class="addPersonalDate">
     <h1 style="text-align: center;font-size: 24px;
    margin-bottom: 25px;"></h1>
    <el-form :data="ruleForm" label-width="100px" class="demo-ruleForm" ref="ruleForm">
      <div>
        <h3 style="margin-bottom:20px;">:</h3>
        <el-form-item label="" prop="name">
          <el-input v-model="ruleForm.name"></el-input>
        </el-form-item>
        <el-form-item label="" prop="age">
          <el-input v-model="ruleForm.age"></el-input>
        </el-form-item>
        <el-form-item label="" prop="phone">
          <el-input v-model="ruleForm.phone"></el-input>
        </el-form-item>
      </div>
      <div>
        <el-form-item label="" prop="nation">
          <el-input v-model="ruleForm.nation"></el-input>
        </el-form-item>
        <el-form-item label="QQ" prop="qq">
          <el-input v-model="ruleForm.qq"></el-input>
        </el-form-item>
        <el-form-item label="" prop="PHONE">
          <el-input v-model="ruleForm.PHONE"></el-input>
        </el-form-item>
      </div>
      <div>
        <el-form-item label="" prop="registration">
          <el-input v-model="ruleForm.registration"></el-input>
        </el-form-item>
        <el-form-item label="" prop="email">
          <el-input v-model="ruleForm.email"></el-input>
        </el-form-item>
        <el-form-item label="" prop="idCard">
          <el-input v-model="ruleForm.idCard"></el-input>
        </el-form-item>
      </div>
        <el-form-item label="" prop="height">
          <el-input v-model="ruleForm.height"></el-input>
        </el-form-item>
        <el-form-item label="" prop="sex" class="sexx">
          <el-radio-group v-model="ruleForm.sex">
            <el-radio :label="1"></el-radio>
            <el-radio :label="2"></el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="" prop="politics" style="margin-left:25px;">
          <el-input v-model="ruleForm.politics"></el-input>
        </el-form-item>
        <h3 style="margin-bottom:20px;"></h3>
        <el-form-item label="" prop="school">
          <el-input v-model="ruleForm.school"></el-input>
        </el-form-item>
        <el-form-item label="" prop="profession">
          <el-input v-model="ruleForm.profession"></el-input>
        </el-form-item>
        <el-form-item label="" prop="graduationTime" class="demonstration">
          <el-date-picker v-model="ruleForm.graduationTime" type="date" placeholder="" :picker-options="pickerOptions"></el-date-picker>
        </el-form-item>
        <h3 style="margin-bottom:20px;">:</h3>
        <el-form-item label="" prop="getCertificate">
          <el-input v-model="ruleForm.getCertificate"></el-input>
        </el-form-item>
        <el-form-item label="" prop="getTime">
          <el-date-picker v-model="ruleForm.getTime" type="date" placeholder="" :picker-options="pickerOptions"></el-date-picker>
        </el-form-item>
        <el-form-item label="" prop="certificateNumber">
          <el-input v-model="ruleForm.certificateNumber"></el-input>
        </el-form-item>
        <h3 style="margin-bottom:20px;">:</h3>
        <el-form-item label="" prop="job">
          <el-input v-model="ruleForm.job"></el-input>
        </el-form-item>
        <el-form-item label="" prop="department">
          <el-input v-model="ruleForm.department"></el-input>
        </el-form-item>
        <el-form-item label=":" prop="hiredate">
          <el-date-picker v-model="ruleForm.hiredate" type="date" placeholder="" :picker-options="pickerOptions"></el-date-picker>
        </el-form-item>
        <el-form-item class="center">
          <el-button type="primary" @click="onSubmit"></el-button>
          <el-button @click="offReset"></el-button>
        </el-form-item>
    </el-form>
  </div>
</template>
<script type="text/ecmascript-6">
export default {
  data () {
    return {
      ruleForm: {
        name: '',
        age: '',
        phone: '',
        nation: '',
        qq: '',
        PHONE: '',
        registration: '',
        email: '',
        height: '',
        sex: 2,
        politics: '',
        school: '',
        profession: '',
        graduationTime: '',
        getCertificate: '',
        getTime: '',
        certificateNumber: '',
        job: '',
        department: '',
        hiredate: ''
      }
    };
  },
  methods: {
    onSubmit () {
      console.log('', JSON.stringify(this.ruleForm));
    },
    offReset () {
      this.ruleForm = {
        name: '',
        age: '',
        phone: '',
        nation: '',
        qq: '',
        PHONE: '',
        registration: '',
        email: '',
        height: '',
        sex: 2,
        politics: '',
        school: '',
        profession: '',
        graduationTime: '',
        getCertificate: '',
        getTime: '',
        certificateNumber: '',
        job: '',
        department: '',
        hiredate: ''
      };
    },
    pickerOptions: {
      disabledDate (time) {
        return time.getTime() < Date.now - 8.64e7;
      }
    }
  }
};
</script>
<style type="text/css">
  .addPersonalDate .el-input, .addPersonalDate .el-input__inner {
    width: 170px;
    display: inline-block;
  }
  .addPersonalDate .el-form-item {
    margin-bottom: 22px;
    margin-right: 50px;
    display: inline-block;
  }
  .addPersonalDate .el-radio {
    margin-left: 15px;
    padding-left: 20px;
  },
  .addPersonalDate .sexx {
    margin-right: 30px;
  }
  .addPersonalDate .center{
      padding-left: 300px;
      margin-top: 50px;
    }
</style>


