<template>
  <div class="patientFeedback">
    <span class="title"></span>
    <div class="form">
      <el-form :model="ruleForm" :inline="true" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
        <el-form-item label="" required>
           <el-col :span="11">
            <el-form-item prop="date1">
              <el-date-picker type="date" placeholder="" v-model="ruleForm.date1" style="width: 150px;"></el-date-picker>
            </el-form-item>
          </el-col>
        </el-form-item>
        <el-form-item label="" required>
           <el-col :span="11">
            <el-form-item prop="date2">
              <el-date-picker type="date" placeholder="" v-model="ruleForm.date2" style="width: 150px;"></el-date-picker>
            </el-form-item>
          </el-col>
        </el-form-item>      
      </el-form>
      <el-form :model="ruleForm" :rules="rules" ref="ruleForm">
        <el-form-item label="" prop="answer" required>
          <el-checkbox-group v-model="ruleForm.answer1" :max="1">
            <el-checkbox label="checkbox1" name="answer"></el-checkbox>
            <el-checkbox label="checkbox2" name="answer"></el-checkbox>
            <el-checkbox label="checkbox3" name="answer"></el-checkbox>
          </el-checkbox-group>
        </el-form-item>
        <el-form-item label="" prop="answer" required>
          <el-checkbox-group v-model="ruleForm.answer2" :max="1">
            <el-checkbox label="checkbox1" name="answer"></el-checkbox>
            <el-checkbox label="checkbox2" name="answer"></el-checkbox>
            <el-checkbox label="checkbox3" name="answer"></el-checkbox>
          </el-checkbox-group>
        </el-form-item>
        <el-form-item label="" prop="answer" required>
          <el-checkbox-group v-model="ruleForm.answer3" :max="1">
            <el-checkbox label="checkbox1" name="answer"></el-checkbox>
            <el-checkbox label="checkbox2" name="answer"></el-checkbox>
            <el-checkbox label="checkbox3" name="answer"></el-checkbox>
          </el-checkbox-group>
        </el-form-item>
        <el-form-item label="" prop="answer" required>
          <el-checkbox-group v-model="ruleForm.answer4" :max="1">
            <el-checkbox label="checkbox1" name="answer"></el-checkbox>
            <el-checkbox label="checkbox2" name="answer"></el-checkbox>
            <el-checkbox label="checkbox3" name="answer"></el-checkbox>
          </el-checkbox-group>
        </el-form-item>
        <el-form-item label="" prop="answer" required>
          <el-checkbox-group v-model="ruleForm.answer5" :max="1">
            <el-checkbox label="checkbox1" name="answer"></el-checkbox>
            <el-checkbox label="checkbox2" name="answer"></el-checkbox>
            <el-checkbox label="checkbox3" name="answer"></el-checkbox>
          </el-checkbox-group>
        </el-form-item>
        <el-form-item label="" prop="answer" required>
          <el-checkbox-group v-model="ruleForm.answer6" :max="1">
            <el-checkbox label="checkbox1" name="answer"></el-checkbox>
            <el-checkbox label="checkbox2" name="answer"></el-checkbox>
            <el-checkbox label="checkbox3" name="answer"></el-checkbox>
          </el-checkbox-group>
        </el-form-item>
        <el-form-item label="" prop="answer" required>
          <el-checkbox-group v-model="ruleForm.answer7" :max="1">
            <el-checkbox label="checkbox1" name="answer"></el-checkbox>
            <el-checkbox label="checkbox2" name="answer"></el-checkbox>
            <el-checkbox label="checkbox3" name="answer"></el-checkbox>
          </el-checkbox-group>
        </el-form-item>
        <el-form-item label="" prop="answer" required>
          <el-checkbox-group v-model="ruleForm.answer8" :max="1">
           <el-checkbox label="checkbox1" name="answer"></el-checkbox>
            <el-checkbox label="checkbox2" name="answer"></el-checkbox>
            <el-checkbox label="checkbox3" name="answer"></el-checkbox>
          </el-checkbox-group>
        </el-form-item>
        <el-form-item label="" prop="answer" required>
          <el-checkbox-group v-model="ruleForm.answer9" :max="1">
            <el-checkbox label="checkbox1" name="answer"></el-checkbox>
            <el-checkbox label="checkbox2" name="answer"></el-checkbox>
            <el-checkbox label="checkbox3" name="answer"></el-checkbox>
          </el-checkbox-group>
        </el-form-item>
        <el-form-item label="" prop="answer" required>
          <el-checkbox-group v-model="ruleForm.answer10" :max="1">
            <el-checkbox label="checkbox1" name="answer"></el-checkbox>
            <el-checkbox label="checkbox2" name="answer"></el-checkbox>
            <el-checkbox label="checkbox3" name="answer"></el-checkbox>
          </el-checkbox-group>
        </el-form-item>
        <el-form-item label="" prop="answer" required>
          <el-checkbox-group v-model="ruleForm.answer11" :max="1">
            <el-checkbox label="checkbox1" name="answer"></el-checkbox>
            <el-checkbox label="checkbox2" name="answer"></el-checkbox>
            <el-checkbox label="checkbox3" name="answer"></el-checkbox>
          </el-checkbox-group>
        </el-form-item>
      </el-form>

      
      <el-form>
          <el-form-item>
            <el-button type="primary" @click="onSubmit('ruleForm')"></el-button>
            <el-button @click="resetForm('ruleForm')"></el-button>
          </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  export default {
    data () {
      return {
        ruleForm: {
          date1: '',
          date2: '',
          answer1: [],
          answer2: [],
          answer3: [],
          answer4: [],
          answer5: [],
          answer6: [],
          answer7: [],
          answer8: [],
          answer9: [],
          answer10: [],
          answer11: []
        },
        rules: {
          date1: [
            { type: 'date', required: true, message: '', trigger: 'change' }
          ],
          date2: [
            { type: 'date', required: true, message: '', trigger: 'change' }
          ],
          answer1: [
            { required: true, message: '  ', trigger: 'blur' }
          ],
          answer2: [
            { required: true, message: '  ', trigger: 'change' }
          ],
          answer3: [
            { required: true, message: '  ', trigger: 'change' }
          ],
          answer4: [
            { required: true, message: '  ', trigger: 'change' }
          ],
          answer5: [
            { required: true, message: '  ', trigger: 'change' }
          ],
          answer6: [
            { required: true, message: '  ', trigger: 'change' }
          ],
          answer7: [
            { required: true, message: '  ', trigger: 'change' }
          ],
          answer8: [
            { required: true, message: '  ', trigger: 'change' }
          ],
          answer9: [
            { required: true, message: '  ', trigger: 'change' }
          ],
          answer10: [
            { required: true, message: '  ', trigger: 'change' }
          ],
          answer11: [
            { required: true, message: '  ', trigger: 'change' }
          ]
        }
      };
    },
    methods: {
      onSubmit (formName) {
        this.timeFormat();
        this.$refs[formName].validate((valid) => {
          if (valid) {
            this.$message({
              message: '',
              type: 'success'
            });
            console.log(this.ruleForm);
          } else {
            this.$message.error('');
            return false;
          }
        });
      },
      resetForm (formName) {
        this.$refs[formName].resetFields();
      },
      timeFormat () {
        let oldDate1 = this.ruleForm.date1;
        let oldDate2 = this.ruleForm.date2;
        let year1 = new Date(oldDate1).getFullYear();
        let month1 = new Date(oldDate1).getMonth() + 1;
        let day1 = new Date(oldDate1).getDate();
        let year2 = new Date(oldDate2).getFullYear();
        let month2 = new Date(oldDate2).getMonth() + 1;
        let day2 = new Date(oldDate2).getDate();
        let dateFormat1 = year1 + '-' + month1 + '-' + day1;
        let dateFormat2 = year2 + '-' + month2 + '-' + day2;
        console.log('', dateFormat1);
        console.log('', dateFormat2);
      }
    }
  };
</script>

<style lang="stylus-loader" rel="stylesheet/stylus">
  .patientFeedback .title
    font-size:24px
    margin-left:300px
  .patientFeedback .form
     margin-top:80px
  .patientFeedback
    height:850px
    width:900px
    margin:auto
    border:1px solid black
  .patientFeedback .checkbox
    padding-bottom:50px
  .patientFeedback .question
    padding-right:100px

</style>


