<template>
  <div class="drugApplyC">
    <el-form label-width="120px" :model="applytable" :rules="rules" ref="applyform" class="apply-form">
      <h4 class="applytable"></h4>
      <p class="radio-p">
        <el-radio label="1" v-model="applytable.checked"></el-radio>
        <el-radio label="2" disabled></el-radio>
      </p>
      <el-form-item label="" prop="drugname">
        <el-input v-model="applytable.drugname"></el-input>
      </el-form-item>
      <el-form-item label="OTC" prop="otc_value">
        <el-select v-model="applytable.otc_value" placeholder="">
          <el-option
            v-for="item in OTC"
            :key="item.otc_value"
            :label="item.label"
            :value="item.otc_value">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="" prop="form_value">
        <el-select v-model="applytable.form_value" placeholder="">
          <el-option
            v-for="f in form"
            :key="f.form_value"
            :label="f.label"
            :value="f.form_value">
          </el-option>
        </el-select>
      </el-form-item>              
      <el-form-item label="" prop="drugform">
        <el-input v-model="applytable.drugform"></el-input>
      </el-form-item>
      <el-form-item label="" prop="drugquantity">
        <el-input v-model="applytable.drugquantity"></el-input>
      </el-form-item>
      <el-form-item label="" prop="applyman">
        <el-input v-model="applytable.applyman"></el-input>
      </el-form-item>
      <el-form-item label="" prop="applyoffices">
        <el-input v-model="applytable.applyoffices"></el-input>
      </el-form-item>      
      <el-form-item label="" prop="date">
        <el-date-picker v-model="applytable.date" type="date" placeholder=""></el-date-picker>
      </el-form-item>
      <p  class="submit">
        <el-button type="reset" @click="resetApply"></el-button>
        <el-button type="submit" @click="submitApply"></el-button>
      </p>         
    </el-form>
    <h4 class="tips">10</h4>
    <el-table :data="insufficientTable" border class="insufficientTable">
      <el-table-column prop="drugname" label=""></el-table-column>
      <el-table-column prop="type" label=""></el-table-column>
      <el-table-column prop="quantity" label=""></el-table-column>
    </el-table>
  </div>
</template>

<script type="text/ecmascript-6">
  import {api} from '../../../global/api.js';
  export default {
    data () {
      let validatevalue = (rule, value, callback) => {
        if (value === '') {
          callback(new Error(''));
        } else {
          callback();
        }
      };
      let checkquantity = (rule, value, callback) => {
        if (!value) {
          return callback(new Error(''));
        }
        setTimeout(() => {
          if (/^\d+$/.test(value) === false) {
            callback(new Error(''));
          } else {
            callback();
          }
        }, 1000);
      };
      return {
        OTC: [
          {
            otc_value: '1',
            label: ''
          },
          {
            otc_value: '2',
            label: ''
          }
        ],
        // 
        form: [
          {
            form_value: '1',
            label: ''
          },
          {
            form_value: '2',
            label: ''
          },
          {
            form_value: '3',
            label: ''
          },
          {
            form_value: '4',
            label: ''
          },
          {
            form_value: '5',
            label: ''
          },
          {
            form_value: '6',
            label: ''
          },
          {
            form_value: '7',
            label: ''
          },
          {
            form_value: '8',
            label: ''
          },
          {
            form_value: '9',
            label: ''
          }
        ],
        // 
        applytable: {
          drugname: '',
          drugform: '',
          drugquantity: '',
          applyoffices: '',
          applyman: '',
          checked: '1',
          otc_value: '',
          form_value: '',
          date: ''
        },
        rules: {
          drugname: [
            {validator: validatevalue, trigger: 'blur'}
          ],
          drugform: [
            {validator: validatevalue, trigger: 'blur'}
          ],
          drugquantity: [
            {validator: checkquantity, trigger: 'blur'}
          ],
          applyoffices: [
            {validator: validatevalue, trigger: 'blur'}
          ],
          applyman: [
            {validator: validatevalue, trigger: 'blur'}
          ],
          otc_value: [
            {validator: validatevalue, trigger: 'change'}
          ],
          form_value: [
            {validator: validatevalue, trigger: 'change'}
          ],
          date: [
            {validator: validatevalue, trigger: 'blur'}
          ]
        },
        // 
        insufficientTable: []
      };
    },
    mounted () {
      this.getinsufficientData();
    },
    methods: {
      // 
      submitApply: function () {
        this.$refs.applyform.validate((valid) => {
          if (valid) {
            this.$notify({
              message: '',
              type: 'success'
            });
            // 
            // console.log(this.applytable);
            // 
            // this.$http.post('', {applytable: 'this.applytable'}).then(response => {}, response => {});
          } else {
            this.$notify.error({
              message: ''
            });
            return false;
          }
        });
      },
      // 
      resetApply: function () {
        // vue
        this.$refs.applyform.resetFields();
      },
      // 10
      getinsufficientData: function () {
        let drugapplyThis = this;

        drugapplyThis.$http.get(api.drugs, {params: {q: 1}}).then((response) => {
          // json
          for (let i = 0; i < response.data.tableDataC.length; i++) {
            if (response.data.tableDataC[i].quantity < 10) {
              drugapplyThis.insufficientTable.push(response.data.tableDataC[i]);
            }
          }
        }, response => {
          // error callback
          drugapplyThis.$notify.error({
            message: ''
          });
        });
      }
    }
  };
</script>

<style lang="stylus-loader" rel="stylesheet/stylus">
  .drugApplyC .apply-form
    width: 100%
  .drugApplyC .apply-form .applytable
    display: block
    line-height: 30px
    text-align: center
    font-size: 20px
    font-weight: bold
    margin: 0 auto
  .drugApplyC .apply-form .radio-p
    line-height: 80px
    text-align: center
  .drugApplyC .apply-form .el-form-item
    display: inline-block
    width: 25%
    padding-left: 40px
    padding-right: 40px
  .drugApplyC .apply-form .el-select
    width: 100%
  .drugApplyC .apply-form .submit
    margin-top: 20px
    text-align: center
  .drugApplyC .tips
    display: block
    padding-left: 95px
    margin-top: 20px
    margin-bottom: 20px
    color: #FF4949
  .drugApplyC .insufficientTable
    width: 85%
    margin: 0 auto
</style>



