<template>
  <div class="addTrashy">
    <el-form ref="form" :model="form" label-width="80px" :rules="rules">
      <el-form-item label="" prop="name">
        <el-input v-model.trim="form.name"></el-input>
      </el-form-item>
      <el-form-item label="" prop="amount">
        <el-input v-model.trim.number="form.amount" type="number"></el-input>
      </el-form-item>
      <el-form-item label="" prop="handlers">
        <el-input v-model.trim="form.handlers"></el-input>
      </el-form-item>
      <el-form-item label="" prop="trashy">
        <el-input v-model.trim="form.trashy" type="textarea"></el-input>
      </el-form-item>
      <el-form-item label=""  prop="manufacturers">
        <el-input v-model.trim="form.manufacturers"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="onSubmit"></el-button>
        <el-button @click="goback"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<script type="text/ecmascript-6">
  import {api} from '../../../../global/api.js';
  export default {
    data () {
      return {
        form: {
          name: '',
          amount: '',
          handlers: '',
          trashy: '',
          manufacturers: ''
        },
        rules: {
          name: [
            { required: true, message: '', trigger: 'blur' }
          ],
          handlers: [
            { required: true, message: '', trigger: 'blur' }
          ],
          manufacturers: [
            { required: true, message: '', trigger: 'blur' }
          ],
          trashy: [
            { required: true, message: '', trigger: 'blur' }
          ]
        }
      };
    },
    methods: {
      onSubmit () {
        console.log(this.form);
        this.$http.get(api.Storage, {params: {form: this.form}}).then(function (response) {
          this.$message({
            message: '',
            type: 'success'
          });
          history.go(-1);
        }, function () {
          this.$message.error(',');
        });
      },
      goback () {
        history.go(-1);
      }
    }
  };
</script>

<style lang="stylus-loader" rel="stylesheet/stylus">
  .addTrashy
    max-width:850px
</style>



