<template>
  <div class="stockRemoval">
    <el-table :data="drug">
      <el-table-column prop="name" label=""></el-table-column>
      <el-table-column prop="amount" label=""></el-table-column>
      <el-table-column prop="handlers" label=""></el-table-column>
      <el-table-column prop="timeApplication" label=""></el-table-column>
      <el-table-column prop="outOfTime" label=""></el-table-column>
      <el-table-column prop="manufacturers" label=""></el-table-column>
      <el-table-column label="">
        <template prop="outOfTime" scope="scope">
          <el-button v-if="!drug[scope.$index].outOfTime" size="small" @click="handleChange(scope.$index, scope.row)" type="danger"></el-button>
          <el-button class="handleOut" v-else="drug[scope.$index].outOfTime" size="small"></el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script type="text/ecmascript-6">
  import {api} from '../../../global/api.js';
  export default {
    data () {
      return {
        drug: []
      };
    },
    created () {
      this.$http.get(api.stockRemoval).then((response) => {             // mark
        this.drug = response.body.drug;
        console.log(this.drug);
      }, response => {
      // error callback
        this.$message({
          message: '',
          type: 'error'
        });
      });
    },
    methods: {
      handleChange (index, row) {
        let time = new Date();
        this.drug[index].outOfTime = time.getFullYear() + '-' + time.getMonth() + '-' + time.getDay() + ' ' + time.getHours() + ':' + time.getMinutes() + ':' + time.getSeconds();
        console.log(index, this.drug, this.drug[index].id, this.drug[index].outOfTime);
        this.$http.get(api.stockRemoval, {params: {id: this.drug[index].id, outOfTime: this.drug[index].outOfTime}}).then(function (response) {
          this.$message({
            message: '',
            type: 'success'
          });
        }, function () {
          this.$message.error(',');
        });
      }
    }
  };
</script>

<style lang="stylus-loader" rel="stylesheet/stylus">
.stockRemoval
  .handleOut
    cursor:no-drop
    &:hover,&:focus
      color:#1f2d3d
      border-color:#c4c4c4
</style>



