<template>
    <div class="addDoctor">
      <span class="tittle"></span>
      <el-form ref="form" :model="form" :rules="rules" label-width="80px" class="addForm">
        <el-form-item label="" prop="name">
          <el-input v-model="form.name"></el-input>
        </el-form-item>
        <el-form-item label="" prop="sex">
          <el-radio class="radio" v-model="form.sex" label="man"></el-radio>
          <el-radio class="radio" v-model="form.sex" label="woman"></el-radio>
        </el-form-item>
        <el-form-item label="" prop="date">
          <el-col :span="11">
            <el-date-picker type="date" placeholder="" v-model="form.date" ></el-date-picker>
          </el-col>
        </el-form-item>
        <el-form-item label="" prop="degree">
          <el-select v-model="form.degree" placeholder="">
            <el-option label="" value="junior"></el-option>
            <el-option label="" value="undergraduate"></el-option>
            <el-option label="" value="postgraduate"></el-option>
            <el-option label="" value="doctor"></el-option>
            <el-option label="" value="master"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="major">
          <el-input v-model="form.major"></el-input>
        </el-form-item>
        <el-form-item label="" prop="profession">
          <el-input v-model="form.profession"></el-input>
        </el-form-item>
        <el-form-item label="" prop="office">
          <el-input v-model="form.office"></el-input>
        </el-form-item>
        <el-form-item class="buttonAdd">
          <el-button type="primary" @click="submitForm('form')"> </el-button>
          <el-button type="danger" @click="resetForm ('form')"> </el-button>
        </el-form-item>
      </el-form>
    </div>
</template>

<script type="text/ecmascript-6">
    export default {
      data () {
        return {
          form: {
            name: '',
            sex: 'man',
            date: '',
            degree: '',
            major: '',
            profession: '',
            office: ''
          },
          // 
          rules: {
            name: [
              { required: true, message: '', trigger: 'blur' },
              { min: 2, max: 5, message: ' 3  5 ', trigger: 'blur' }
            ],
            sex: [
              { required: true, message: '', trigger: 'change' }
            ],
            date: [
              { type: 'date', required: true, message: '', trigger: 'blur' }
            ],
            degree: [
              { required: true, message: '', trigger: 'change' }
            ],
            major: [
              { required: true, message: '', trigger: 'blur' }
            ],
            profession: [
              { required: true, message: '', trigger: 'blur' }
            ],
            office: [
              { required: true, message: '', trigger: 'blur' }
            ]
          }
        };
      },
      methods: {
        // 
        submitForm (form) {
        // 
          this.$refs[form].validate((valid) => {
            if (valid) {
        // 
              this.$message({
                showClose: true,
                message: '',
                type: 'success'
              });
            } else {
              this.$message({
                showClose: true,
                message: '',
                type: 'error'
              });
              return false;
            }
          });
        },
        // 
        resetForm (form) {
          this.$refs[form].resetFields();
        }
      }
};

</script>

<style lang="stylus-loader" rel="stylesheet/stylus">
.addDoctor .el-input
  width:384px
.addDoctor .tittle
  display:block
  text-align: center
  font-size:24px
  font-weight:600
  line-height:80px
.addDoctor .addForm
  width:470px
  margin:auto
.addDoctor .addForm .buttonAdd
  text-align: center;
</style>


