<template>
  <div class="prescriptionCManageC">
    <!--  -->
    <div class="filter-container">
      <el-input placeholder="" v-model="searchNumber"></el-input>
      <el-input placeholder="" v-model="searchName"></el-input>
      <el-button type="primary" icon="search" @click="search"></el-button>
      <el-button type="primary" icon="view" @click="showAll"></el-button>
    </div>    
    <el-table :data="prescriptionC">
      <el-table-column type="expand">
        <template scope="props">
          <el-form label-position="left" inline class="form-expand">
            <el-form-item label="">
              <span>{{ props.row.disease }}</span>
            </el-form-item>
            <el-form-item label="">
              <span>{{ props.row.doctor }}</span>
            </el-form-item>
            <el-form-item label="">
              <span>{{ props.row.date }}</span>
            </el-form-item>
            <hr class="hr">
            <el-form-item label="" class="symptoms">
              <span>{{ props.row.symptoms }}</span>
            </el-form-item>
            <hr class="hr">
            <h5 class="prescriptionC-title">R</h5>
            <template>
              <el-table :data="props.row.prescription.drug" style="width: 80%; margin: 0 auto;">
                <el-table-column prop="sort" label="" width="100">
                </el-table-column>
                <el-table-column prop="drugname" label="" width="200">
                </el-table-column>
                <el-table-column prop="quantity" label="/" width="180">
                </el-table-column>
                <el-table-column prop="usage" label="" width="260">
                </el-table-column>                
              </el-table>
            </template>
            <el-form-item label="" class="Rp">
              <span>{{ props.row.prescription.tips }}</span>
            </el-form-item>                         
          </el-form> 
        </template>
      </el-table-column>
      <el-table-column label="" prop="number">
      </el-table-column>
      <el-table-column label="" prop="name">
      </el-table-column>
      <el-table-column label="" prop="sex">
      </el-table-column>
      <el-table-column label="" prop="age">
      </el-table-column>
      <el-table-column label="" prop="position">
      </el-table-column>    
      <el-table-column label="" prop="phone">
      </el-table-column>      
      <el-table-column label="" prop="pharmacist">
      </el-table-column>
      <el-table-column label="" width="210">
        <template scope="scope">
          <el-button type="primary" @click="refuseprescriptionC(scope.$index)"></el-button>
          <el-button type="primary" @click="acceptprescriptionC(scope.$index)"></el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script type="text/ecmascript-6">
  import {api} from '../../../global/api.js';
  export default {
    data () {
      return {
        prescriptionC: [],
        intendedprescription: [],
        drugs: [],
        searchNumber: '',
        searchName: ''
      };
    },
    mounted () {
      let prescriptionCThis = this;

      prescriptionCThis.$http.get(api.patientList).then((response) => {
        // response
        // console.log(prescriptionCThis.prescriptionC, response);
        // json
        prescriptionCThis.prescriptionC = response.data.tableData;
        prescriptionCThis.intendedprescription = response.data.tableData;
      }, response => {
        // error callback
        prescriptionCThis.$notify.error({
          message: ''
        });
      });
      prescriptionCThis.$http.get(api.drugs).then((response) => {
        // json
        prescriptionCThis.drugs = response.data.tableDataC;
      }, response => {
        // error callback
        prescriptionCThis.$notify.error({
          message: ''
        });
      });
    },
    methods: {
      // 
      search () {
        let search = this;
        search.$http.get(api.patientList, {params: {number: search.searchNumber, name: search.searchName}}).then((response) => {
          // 
          search.prescriptionC = [];
          // pushprescriptionC[]
          for (let i = 0; i < response.data.tableData.length; i++) {
            // 
            if (search.searchNumber && search.searchName) {
              if (response.data.tableData[i].number === search.searchNumber && response.data.tableData[i].name === search.searchName) {
                search.prescriptionC.push(response.data.tableData[i]);
              }
            } else {
              if (search.searchNumber && response.data.tableData[i].number === search.searchNumber) {
                // 
                search.prescriptionC.push(response.data.tableData[i]);
              } else if (search.searchName && response.data.tableData[i].name === search.searchName) {
                // 
                search.prescriptionC.push(response.data.tableData[i]);
              }
            }
          }
        }, (response) => {
          // error callback
          search.$notify.error({
            message: ''
          });
        });
      },
      // 
      showAll () {
        this.prescriptionC = this.intendedprescription;
        this.searchNumber = '';
        this.searchNamer = '';
      },
      // 
      refuseprescriptionC: function (index) {
        // 
        this.$message({
          message: '' + this.prescriptionC[index].doctor + '',
          type: 'success'
        });
      },
      // 
      acceptprescriptionC: function (index) {
        // this.prescriptionC[index].pharmacist
        //  
        this.prescriptionC[index].pharmacist = sessionStorage.getItem('easeHis');
        this.$message({
          message: '' + this.prescriptionC[index].pharmacist + '',
          type: 'success'
        });
        // mark!!! -
        for (let i = 0; i < this.drugs.length; i++) {
          for (let j = 0; j < this.prescriptionC[index].prescription.drug.length; j++) {
            if (this.drugs[i].drugname === this.prescriptionC[index].prescription.drug[j].drugname) {
              this.drugs[i].quantity = this.drugs[i].quantity - this.prescriptionC[index].prescription.drug[j].quantity;
              // 
              // this.$http.post('', {drugs: 'this.drugs'}).then(response => {}, response => {});
              // 
              console.log(this.drugs[i].quantity);
            };
          };
        };
      }
    }
  };
</script>

<style lang="stylus-loader" rel="stylesheet/stylus">
  .prescriptionCManageC .filter-container
    padding-bottom:30px
    .el-input
      width: 200px  
  .prescriptionCManageC
    .form-expand
      /* background: #99FF66 */
      width:80%
      margin: 0 auto
      .el-form-item
        width: 29%
        display: inline-block
        margin-left: 20px
      .symptoms
        width: 100%
      .Rp
        width: 80%
        margin-left: 10%
      .hr
        margin-top: -25px
        border-top: 1px solid #4A7C32
      .prescriptionC-title
        font-size: 26px
        margin-left: 20px
  .prescriptionCManageC .form-expand .el-form-item span
    display: block
    font-size: 15px
    font-weight: bold

</style>



