<template>
  <div class="departure">
    <h1 style="text-align: center;font-size: 24px;
    margin-bottom: 25px;"></h1>
    <el-form :data="ruleForm" label-width="100px" class="demo-ruleForm" ref="ruleForm">
      <h2 style="margin-bottom:20px;">:</h2>
      <div>
        <el-form-item label="" prop="name">
          <el-input v-model="ruleForm.name"></el-input>
        </el-form-item>
        <el-form-item label="/" prop="department">
          <el-input v-model="ruleForm.department"></el-input>
        </el-form-item>
        <el-form-item label="/" prop="jobs">
          <el-input v-model="ruleForm.jobs"></el-input>
        </el-form-item>
      </div>
      <div>
        <el-form-item label="" prop="hiredate" class="demonstration">
          <el-date-picker v-model="ruleForm.hiredate" type="date" placeholder="" :picker-options="pickerOptions"></el-date-picker>
        </el-form-item>
        <el-form-item label="" prop="leaveDate">
          <el-date-picker v-model="ruleForm.leaveDate" type="date" placeholder="" :picker-options="pickerOptions"></el-date-picker>
        </el-form-item>
        <el-form-item label="" prop="education">
          <el-input v-model="ruleForm.education"></el-input>
        </el-form-item>
      </div>
      <h2 style="margin-bottom:20px;"></h2>
      <div>
        <el-checkbox-group v-model="ruleForm.category">
          <el-checkbox label=""></el-checkbox>
          <el-checkbox label=""></el-checkbox>
          <el-checkbox label=""></el-checkbox>
          <el-checkbox label=""></el-checkbox>
          <el-checkbox label=""></el-checkbox>
          <el-checkbox label=""></el-checkbox>
        </el-checkbox-group>
      </div>
      <h2 style="margin-bottom:20px;"></h2>
      <div>
        <el-form-item label="" prop="applicationTime" class="demonstration">
          <el-date-picker v-model="ruleForm.applicationTime" type="date" placeholder="" :picker-options="pickerOptions"></el-date-picker>
        </el-form-item>
        <el-form-item label="" prop="approvedTime">
          <el-date-picker v-model="ruleForm.approvedTime" type="date" placeholder="" :picker-options="pickerOptions"></el-date-picker>
        </el-form-item>
        <el-form-item label="" prop="reason">
          <el-input v-model="ruleForm.reason"></el-input>
        </el-form-item>
      </div>
      <span style="margin-bottom:20px;">
          
        :______________________
      </span>
      <div>
        <h2 style="margin:20px 0px;">:</h2>
        <el-form-item label="" prop="departmentManager">
          <el-input v-model="ruleForm.departmentManager"></el-input>
        </el-form-item>
        <el-form-item label="" prop="director">
          <el-input v-model="ruleForm.director"></el-input>
        </el-form-item>
        <span>:1.</span>
      </div>
      <div>
        <el-form-item class="center">
          <el-button type="primary" @click="onSubmit"></el-button>
          <!-- <el-button @click="resetForm('ruleForm')"></el-button> -->
          <el-button @click="offReset"></el-button>
        </el-form-item>
      </div>
    </el-form>
  </div>
</template>
<script type="text/ecmascript-6">
export default {
  data () {
    return {
      ruleForm: {
        name: '',
        department: '',
        jobs: '',
        hiredate: '',
        leaveDate: '',
        organization: '',
        education: '',
        category: [],
        departments: '',
        applicationTime: '',
        approvedTime: '',
        reason: '',
        departmentManager: '',
        director: ''
      }
    };
  },
  methods: {
    onSubmit () {
      // let me = this;
      // this.$http.post(api.addPatient).then(function (response) {
      //   console.log('json', response.ruleForm);
      //   me.ruleForm = me.data.ruleForm;
      // }, function (response) {
      //   alert('');
      // });
      console.log('', JSON.stringify(this.ruleForm));
    },
    offReset () {
      this.ruleForm = {
        name: '',
        department: '',
        jobs: '',
        hiredate: '',
        leaveDate: '',
        organization: '',
        education: '',
        category: '',
        departments: '',
        applicationTime: '',
        approvedTime: '',
        reason: '',
        departmentManager: '',
        director: ''
      };
    },
    pickerOptions: {
      disabledDate (time) {
        return time.getTime() < Date.now - 8.64e7;
      }
    }

    // resetForm (formName) {
    //   this.$refs[formName].resetFields();
    // }
  }
};
</script>
<style type="text/css">
.departure .el-input, .departure .el-input__inner {
  width: 170px; 
  display: inline-block;
}
.departure .el-form-item {
  margin-bottom: 22px;
  margin-right: 50px;
  display: inline-block;
}
.departure .el-checkbox, .departure .el-checkbox__input {
    cursor: pointer;
    display: inline-block;
    position: relative;
    white-space: nowrap;
    margin-left: 29px;
}
.departure .center{
  padding-left: 300px;
  margin-top: 50px;
}
</style>


