<template>
  <div class="hasPatient">
    <div class="filter-container">
      <el-input v-model="listQuery.title" placeholder="" style="width:195px;"></el-input>
      <el-select v-model="listQuery.select" placeholder="" style="width:150px;">
        <el-option label="" value="1"></el-option>
        <el-option label="" value="2"></el-option>
        <el-option label="" value="3"></el-option>
      </el-select>
      <el-button type="primary" icon="search" @click="handleSearch"></el-button>
      <el-button type="primary" icon="edit" @click="handleCreate"></el-button>
      <el-button type="primary" icon="delete" @click="handleDelAll"></el-button>
      <el-button type="primary" @click="handleDownload"><i class="el-icon-document el-icon--left"></i></el-button>
    </div><br><br>
    <el-table :data="tableData" border style="width:100%;" @selection-change="handleSelectionChange">
      <el-table-column type="selection"></el-table-column>
      <el-table-column align="center" label=''>
        <template scope="scope">
          {{scope.$index}}
        </template>
      </el-table-column>
      <el-table-column label="" prop="date"></el-table-column>
      <el-table-column label="" prop="name"></el-table-column>
      <el-table-column label="" prop="sex"></el-table-column>
      <el-table-column label="" prop="age"></el-table-column>
      <el-table-column label="" prop="doctor"></el-table-column>
      <el-table-column type="expand">
        <template scope="props">
          <el-form label-position="left" inline class="demo-table-expand">
            <el-form-item label="">
              <span>{{ props.row.date }}</span>
            </el-form-item>
            <el-form-item label="">
              <span>{{ props.row.name }}</span>
            </el-form-item>
            <el-form-item label="">
              <span>{{ props.row.region }}</span>
            </el-form-item>
            <el-form-item label="">
              <span>{{ props.row.idCard }}</span>
            </el-form-item>
            <el-form-item label="">
              <span>{{ props.row.age }}</span>
            </el-form-item>
            <el-form-item label="">
              <span>{{ props.row.phone }}</span>
            </el-form-item>
            <el-form-item label="">
              <span>{{ props.row.sex }}</span>
            </el-form-item>
            <el-form-item label="">
              <span>{{ props.row.address }}</span>
            </el-form-item>
            <el-form-item label="">
              <span>{{ props.row.Name }}</span>
            </el-form-item>
            <el-form-item label="">
              <span>{{ props.row.Phone }}</span>
            </el-form-item>
            <el-form-item label="">
              <span>{{ props.row.relation }}</span>
            </el-form-item>
            <el-form-item label="">
              <span>{{ props.row.symptoms }}</span>
            </el-form-item>
            <el-form-item label="">
              <span>{{ props.row.illHistory }}</span>
            </el-form-item>
            <el-form-item label="">
              <span>{{ props.row.disease }}</span>
            </el-form-item>
            <el-form-item label="">
              <span>{{ props.row.doctor }}</span>
            </el-form-item>
          </el-form>
        </template>
      </el-table-column>
      <el-table-column label="" width="150">
        <template scope="scope">
          <el-button type="text" size="small" @click="handleEdit(scope.$index,scope.row)"></el-button>
          <el-button type="text" size="small" @click="handleDelete(scope.$index, scope.row)"></el-button>
        </template>
     </el-table-column>
    </el-table>
    <!--  -->
    <el-dialog title="" :visible.sync="dialogFormVisible">
      <el-form class="small-space" :model="temp" label-position="left" label-width="70px" style='width: 400px; margin-left:50px;'>
        <el-form-item label="">
          <el-input v-model="temp.date"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="temp.name"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="temp.sex"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="temp.age"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="temp.doctor"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false"> </el-button>
        <el-button type="primary" @click="handleCreateSubmit"> </el-button>
      </div>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="dialogFormEditVisible">
      <el-form class="small-space" :model="Edit" label-position="left" label-width="70px" style='width: 400px; margin-left:50px;'>
        <el-form-item label="">
          <el-input v-model="Edit.date"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="Edit.name"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="Edit.region"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="Edit.idCard"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="Edit.age"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="Edit.phone"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="Edit.sex"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="Edit.address"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="Edit.Name"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="Edit.Phone"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="Edit.relation"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="Edit.symptoms"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="Edit.illHistory"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="Edit.disease"></el-input>
        </el-form-item>
        <el-form-item label="">
         <el-input v-model="Edit.doctor"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormEditVisible = false"> </el-button>
        <el-button type="primary" @click="handleEditSubmit"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script type="text/ecmascript-6">
import {api} from '../../../global/api.js';
import Vue from 'vue';
export default {
  data () {
    return {
      tableData: [],
      listQuery: {
        select: '',
        title: ''
      },
      temp: {
        date: '',
        name: '',
        sex: '',
        age: '',
        doctor: ''
      },
      Edit: [],
        // date: '',
        // name: '',
        // region: '',
        // idCard: '',
        // age: '',
        // phone: '',
        // sex: '',
        // address: '',
        // Name: '',
        // Phone: '',
        // relation: '',
        // symptoms: '',
        // illHistory: '',
        // disease: '',
        // doctor: ''
      // },
      dialogFormEditVisible: false,
      dialogFormVisible: false,
      multipleSelection: []
    };
  },
  created () {
    let vm = this;
    Vue.http.get(api.patientList).then(function (response) {
      console.log(response);
      console.log('json', response.tableData);
      // me.tableData = response.data.tableData;
      vm.tableData = response.data.tableData;
    }, function (response) {
      alert('');
    });
  },
  methods: {
    handleSearch () {
      let me = this;
      me.$http.get(api.patientList1, {params: {name: me.listQuery.title}}).then(function (response) {
        // console.log(response);
        console.log('json', response.data.tableData);
        // me.tableData = response.data.tableData;
        me.tableData = response.data.tableData;
        console.log(response.data.tableData);
      }, function (response) {
        alert('');
      });
    },
    handleEdit (index, row) {
      this.dialogFormEditVisible = true;
      console.log('', index, 'row', row);
      this.Edit = row;// 
      // this.Edit = JSON.parse(JSON.stringify(row));
      // 
    },
    handleEditSubmit () {
      this.Edit;
      this.dialogFormEditVisible = false;
    },
    handleDelete (index, row) {
      var vm = this;
      console.log('row', index, '-----', row);
      vm.tableData.splice(index, 1);
    },
    handleCreate () {
      this.dialogFormVisible = true;
    },
    handleCreateSubmit () {
      var vm = this;
      console.log('', vm.temp);
      vm.tableData
      .push(vm.temp);
      this.dialogFormVisible = false;
      this.temp = {
        date: '',
        name: '',
        sex: '',
        age: '',
        doctor: ''
      };
    },
    handleDelAll () {
      var vm = this;
      console.log('row', vm.multipleSelection);
    },
    handleSelectionChange (val) {
      this.multipleSelection = val;
    },
    handleDownload () {
      var vm = this;
      require.ensure([], () => {
        const { export_json_to_excel } = require('vendor/Export2Excel');
        const tHeader = ['', '', '', '', '', '', ''];
        const filterVal = ['date', 'name', 'sex', 'address', 'phone', 'age', 'doctor'];
        const tableData = vm.tableData;
        const data = vm.formatJson(filterVal, tableData);
        export_json_to_excel(tHeader, data, 'excel');
      });
    },
    formatJson (filterVal, jsonData) {
      return jsonData.map(v => filterVal.map(j => v[j]));
    }
  }
};

</script>
<style type="text/css">
.hasPatient .demo-table-expand {
    font-size: 0;
  }
 .hasPatient .demo-table-expand label {
    width: 90px;
    color: #99a9bf;
  }
 .hasPatient .demo-table-expand .el-form-item {
    margin-right: 0;
    margin-bottom: 0;
    width: 33%;
  }
</style>


