<template>
  <div class="drugInfosW">
    <!--  -->
    <div class="filter-container">
      <el-input placeholder="" v-model="searchValue"></el-input>
      <el-button type="primary" icon="search" @click="search()"></el-button>

      <el-button type="primary" icon="document" @click="handleDownload"></el-button>
      <el-button type="primary" icon="view" @click="showAll"></el-button>
    </div>
    <!--  -->
    <el-table :data="tableData" border style="width: 100%; text-align:left">
      <el-table-column prop="index" label="" width="100"></el-table-column>
      <el-table-column prop="type" label="" width="80"></el-table-column>
      <el-table-column prop="drugname" label="" width="170"></el-table-column>
      <el-table-column prop="OTC" label="OTC" width="80"></el-table-column>
      <el-table-column prop="specification" label="" width="130"></el-table-column>
      <el-table-column prop="form" label="" width="100"></el-table-column>
      <el-table-column prop="expiry_date" label="" width="130"></el-table-column>
      <el-table-column prop="vender" label="" width="238"></el-table-column>
      <el-table-column prop="quantity" label="/" width="110"></el-table-column>
      <el-table-column prop="price" label="" width="110"></el-table-column>
      <el-table-column label="" width="90">
        <template scope="scope">
          <el-button type="primary" @click="checkDetail(scope.$index)"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!--  -->
    <el-dialog title="" :visible.sync="dialogVisible" :before-close="handleClose">
      <ul class="drugdetail">
        <li><h5></h5><span>{{drugdetails.productname}}</span></li>
        <li><h5>:</h5><span>{{drugdetails.form}}</span></li>
        <li><h5>:</h5><span>{{drugdetails.use_quantity}}</span></li>
        <li><h5>:</h5><span>{{drugdetails.brand}}</span></li>
        <li><h5>:</h5><span>{{drugdetails.plantype}}</span></li>
        <li><h5>:</h5><span>{{drugdetails.expiry}}</span></li>
        <li><h5>:</h5><span>{{drugdetails.usage}}</span></li>
        <li><h5>:</h5><span>{{drugdetails.OTC}}</span></li>
        <li><h5>:</h5><span>{{drugdetails.drugname}}</span></li>
        <li><h5>:</h5><span>{{drugdetails.genericname}}</span></li>
        <li><h5>:</h5><span>{{drugdetails.certificate}}</span></li>
        <li><h5>:</h5><span>{{drugdetails.vender}}</span></li>
        <li><h5>: </h5><span>{{drugdetails.specification}}</span></li>
        <li><h5>: </h5><span>{{drugdetails.type}}</span></li>
      </ul>
      <h5 slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false"> </el-button>
        <el-button type="primary" @click="dialogVisible = false"> </el-button>
      </h5>
    </el-dialog>

  </div>
</template>

<script type="text/ecmascript-6">
  import {api} from '../../../global/api.js';
  export default {
    data () {
      return {
        tableData: [],
        assistanttableData: [],
        dialogVisible: false,
        drugdetails: '',
        searchValue: ''
      };
    },
    mounted () {
      let druginfosThis = this;

      druginfosThis.$http.get(api.drugs).then((response) => {
        // response
        // console.log(druginfosThis.tableData, response);
        // json
        druginfosThis.tableData = response.data.tableDataW;
        druginfosThis.assistanttableData = response.data.tableDataW;
      }, response => {
        // error callback
        druginfosThis.$notify.error({
          message: ''
        });
      });
    },
    methods: {
      // dialog
      handleClose (done) {
        this.$confirm('').then(_ => {
          done();
        }).catch(_ => {});
      },
      // 
      checkDetail (index) {
        // console.log(index);
        this.dialogVisible = true;
        this.drugdetails = this.tableData[index];
      },
       // 
      search () {
        let intendedSearch = this;
        intendedSearch.$http.get(api.drugs, {params: {drugname: intendedSearch.searchValue}}).then((response) => {
          // drugnamepushthis.tableData
          // this.tableData
          intendedSearch.tableData = [];
          for (let i = 0; i < response.data.tableDataW.length; i++) {
            if (response.data.tableDataW[i].drugname === intendedSearch.searchValue) {
              intendedSearch.tableData.push(response.data.tableDataW[i]);
            }
          };
        }, response => {
          // error callback
          intendedSearch.$notify.error({
            message: ''
          });
        });
      },
      // 
      showAll () {
        this.tableData = this.assistanttableData;
        this.searchValue = '';
      },
      // excel
      handleDownload () {
        var vm = this;

        require.ensure([], () => {
          const { export_json_to_excel } = require('vendor/Export2Excel');
          const tHeader = ['', '', '', 'OTC', '', '', '', '', ''];
          const filterVal = ['index', 'type', 'drugname', 'OTC', 'specification', 'form', 'vender', 'quantity', 'price'];
          const tableData = vm.tableData;
          const data = vm.formatJson(filterVal, tableData);
          export_json_to_excel(tHeader, data, 'excel');
        });
      },
      formatJson (filterVal, jsonData) {
        return jsonData.map(v => filterVal.map(j => v[j]));
      }
    }
};
</script>

<style lang="stylus-loader" rel="stylesheet/stylus">
  .drugInfosW .filter-container
    padding-bottom:30px
    .el-input
      width: 400px
  .drugInfosW .el-table-column
    font-weight: bold
  .drugInfosW .drugdetail
    h5
      display: inline-block
      width: 100px
      line-height: 30px
      font-weight: bold
</style>



