<template>
  <div class="hospitalized">
    <h3 class="h3"></h3><br>

    <el-form :model="ruleForm" class="demo-ruleForm" ref="ruleForm" label-width="100px" >
      <el-form :inline="true">
        <el-form-item label="" prop="name">
          <el-input v-model="ruleForm.name"></el-input>
        </el-form-item>
        <el-form-item label="" prop="phone">
          <el-input v-model="ruleForm.phone"></el-input>
        </el-form-item>
      </el-form>
      <el-form :inline="true">
        <el-form-item label="" prop="age">
          <el-input v-model="ruleForm.age"></el-input>
        </el-form-item>
        <el-form-item label="" prop="idCard">
          <el-input v-model="ruleForm.idCard"></el-input>
        </el-form-item>
      </el-form>
      <el-form :inline="true">
        <el-form-item label="" prop="sex">
          <el-radio-group v-model="ruleForm.sex">
            <el-radio :label="1" label-width="150px"></el-radio>
            <el-radio :label="2" label-width="150px"></el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="" prop="address" style="margin-left:35px;">
          <el-input type="textarea"  v-model="ruleForm.address" class="address"></el-input>
        </el-form-item>
      </el-form>
      <el-form :inline="true">
        <el-form-item label="" prop="doctor">
          <el-select v-model="ruleForm.doctor" placeholder="">
            <el-option v-for="item in ruleForm.doctors" :key="item.value" :label="item.label" :value="item.value"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="nurse">
          <el-select v-model="ruleForm.nurse" placeholder="">
            <el-option v-for="nurse in ruleForm.nurses" :key="nurse.value" :label="nurse.label" :value="nurse.value"></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <el-form :inline="true">
        <el-form-item label="" prop="illness">
          <el-input type="textarea" v-model="ruleForm.illness" class="illness"></el-input>
        </el-form-item>
      </el-form>
      <el-form :inline="true">
        <el-form-item label="" prop="treatment">
          <el-input type="textarea" v-model="ruleForm.treatment" class="illness"></el-input>
        </el-form-item>
