        </el-form-item>
        <el-form-item label="" prop="outTime">
          <el-date-picker v-model="ruleForm.outTime" type="date" placeholder="" :picker-options="pickerOptions"></el-date-picker>
        </el-form-item>
      </el-form>
      <el-form :inline="true">
        <el-form-item label="" prop="room">
          <el-select v-model="ruleForm.room" placeholder="">
            <el-option label="210" value="1"></el-option>
